import BaseAPI from "./BaseAPI";

export class RestAPI extends BaseAPI {
  get = (url, token) => {
    if (token)
      return this.callAPI(url, "get", {}, { headers: { auth: token } });
    else return this.callAPI(url, "get");
  };

  put = (url, payload, token) => {
    if (token)
      return this.callAPI(url, "put", payload, {
        headers: { auth: token },
      });
    else return this.callAPI(url, "put", payload);
  };

  post = (url, payload, token) => {
    if (token)
      return this.callAPI(url, "post", payload, {
        headers: { auth: token },
      });
    else return this.callAPI(url, "post", payload);
  };

  delete = (url, token) => {
    let accessToken = "Auth" + token;
    if (token)
      return this.callAPI(
        url,
        "delete",
        {},
        { headers: { Authorization: accessToken } }
      );
    else return this.callAPI(url, "delete");
  };
}

export const api = new RestAPI();
