import React, { useEffect } from "react";
import { PopupComplete, PopupFailed } from "../common";
import { useDispatch, useSelector } from "react-redux";
import {
  toggleModalSuccess,
  toggleModalFailure,
  registerSecurityCode,
  changePasswordClick,
} from "../../redux/actions";
import { history } from "../../routes/AppRouter";
import { setItemLs } from "../../../core/api/login";

export const NotFound = () => {
  const success = useSelector((state) => state.modal.modalSuccess);
  const failure = useSelector((state) => state.modal.modalFailure);
  const securityCode = useSelector((state) => state.user.guestSecurityCode);
  const dispatch = useDispatch();

  useEffect(() => {
    const url = window.location.href;
    const code = url.split("security_code=")[1];
    dispatch(registerSecurityCode(code));

    if (securityCode) {
      setItemLs("security_code", securityCode);
      dispatch(changePasswordClick());
      history.push("/");
    }
  }, [securityCode, dispatch]);

  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <button onClick={() => dispatch(toggleModalSuccess())}>Success</button>
      <br />
      <button onClick={() => dispatch(toggleModalFailure())}>Failed</button>
      <br />

      {success ? <PopupComplete /> : null}
      {failure ? <PopupFailed /> : null}

      {/*<h1>Not Found</h1>*/}
    </div>
  );
};
