import React, { useEffect, useState } from "react";
import Select from "react-select";
import { bankData } from "../../views/CreateNewEvent";

export const CustomSelect = ({
  name,
  handler,
  mistake,
  bankSet,
  disabled,
  value,
}) => {
  const [bankName, setBankName] = useState("");

  useEffect(() => {
    if (!bankSet && value)
      setBankName(
        bankData.filter((bank) => bank.bankCode === value)[0].bankName
      );
  }, [value, bankSet]);

  const options = !bankSet
    ? bankData.map((bank) => {
        return {
          value: bank.bankCode,
          label: `${bank.bankCode}-${bank.bankName}`,
        };
      })
    : bankData
        .filter((bank) => bank.bankCode === bankSet)[0]
        .branch.map((branch) => {
          return {
            value: branch,
            label: branch,
          };
        });

  const customStyles = {
    dropdownIndicator: () => ({
      display: "none",
    }),
    indicatorSeparator: () => ({
      display: "none",
    }),
    clearIndicator: () => ({
      display: "none",
    }),
    input: () => ({
      display: "block",
      width: "100%",
      height: "calc(1.5em + .75rem + 2px)",
      padding: ".375rem .75rem",
      fontSize: "1.2rem",
      fontWeight: "400",
      lineHeight: "1.5",
      color: "#495057",
      backgroundColor: disabled ? "#e9ecef" : "#fff",
      backgroundClip: "padding-box",
      transition: "border-color .15s ease-in-out,box-shadow .15s ease-in-out",
      borderBottom: mistake ? "3px solid red" : "3px solid #4c515b",
      borderTop: "none",
      borderLeft: "none",
      borderRight: "none",
      borderRadius: "0",
    }),
    control: () => ({
      border: "none",
    }),
    valueContainer: () => ({
      fontSize: "1.2rem",
    }),
  };

  const handleChanger = (e, name, handle) => {
    if (name === "bank") {
      if (e === null) {
        handle("bank", "");
        handle("branch", "");
      } else {
        handle("bank", e.value);
        handle("branch", "");
      }
    } else e === null ? handle("branch", "") : handle("branch", e.value);
  };

  return (
    <div className="from-wrapper">
      <div className="form-group text-right position-relative">
        <Select
          placeholder={""}
          styles={customStyles}
          isRtl={true}
          onChange={(e) => handleChanger(e, name, handler)}
          options={options}
          isSearchable={true}
          isClearable={true}
          name={name}
          isDisabled={disabled}
          value={
            !bankSet && value
              ? { value: value, label: `${value}-${bankName}` }
              : { value: value, label: value }
          }
        />
      </div>
    </div>
  );
};

export default CustomSelect;
