export const uploadPhoto = (photo, savePhotoInLocalState) => {
  let formData = new FormData();
  formData.append("file", photo.file);

  api
    .post(endpoint.upload, formData)
    .then((res) => {
      savePhotoInLocalState(res.filePath);
    })
    .catch((e) => console.log(e));
};

