import {
  CREATE_EVENT,
  GET_EVENTS,
  ADD_EVENT_DATA,
  CLEAR_CURRENT_EVENT,
} from "../constants";
import { endpoint } from "../../../config";
import { api } from "../../../core/api";
import { getUserInfo } from "./user";
import { dateFormatter } from "../../components/common";

export const createEvent = (event) => {
  return {
    type: CREATE_EVENT,
    payload: event,
  };
};

export const getEvents = (events) => {
  return {
    type: GET_EVENTS,
    payload: events,
  };
};

export const fillCurrentEventData = (data) => {
  return {
    type: ADD_EVENT_DATA,
    payload: data,
  };
};

export const clearCurrentEvent = () => {
  return { type: CLEAR_CURRENT_EVENT };
};

export const getEventsFromServer = (token) => (dispatch) => {
  api.get(endpoint.userEvents, token).then((res) => {
    dispatch(getEvents(res));
  });
};

export const setupEventOnServer = (eventData, token) => (dispatch) => {
  let formdata = new FormData();
  formdata.append("title", eventData.eventName);
  formdata.append("venue", eventData.eventPlace);
  formdata.append("date", dateFormatter(eventData.eventDate));
  formdata.append("type", eventData.eventType);
  formdata.append("thumbnail", eventData.serverImage);

  api
    .post(endpoint.createEvent, formdata, token)
    .then((result) => {
      dispatch(createEvent(result));
      dispatch(clearCurrentEvent());
      dispatch(getEventsFromServer(token));
      dispatch(getUserInfo(token));
    })
    .catch((error) => console.log("error", error));
};
