import React, { useState } from "react";
import classNames from "classnames";
import { options } from "../../constants";

export const Select = ({ error, value, onChange }) => {
  const [isSelectActive, setSelectActive] = useState(false);
  const { errors, touched } = error;

  return (
    <>
      <div
        className={classNames(
          errors && touched && errors
            ? "img-arrow-marketer-top"
            : "img-arrow-marketer",
          isSelectActive && "rotated"
        )}
      />
      <label htmlFor="eventType">סוג האירוע</label>
      <select
        className={classNames(
          "form-control fc type-select",
          errors && touched && errors && "fc-wrong"
        )}
        name="eventType"
        value={value}
        onChange={onChange}
        onClick={() => setSelectActive((prevState) => !prevState)}
        onBlur={() => {
          return isSelectActive
            ? setSelectActive((prevState) => !prevState)
            : null;
        }}
      >
        <option label={" "} disabled />
        {options.map((option) => (
          <option key={option}>{option}</option>
        ))}
      </select>
    </>
  );
};

export default Select;
