import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Formik } from "formik";
import classNames from "classnames";

import { history } from "../../../../routes/AppRouter";
import { getToken } from "../../../../../core/api/login";
import { defaultImage } from "../../../../../config";
import {
  emailValidator,
  Footer,
  Hero,
  ValidationMistakeText,
  CustomSelect,
} from "../../../common";
import {
  stepTwoValues,
  InnerHero,
  isValidIsraeliID,
  validator,
  uploadPhoto,
} from "../index";
import {
  createUser,
  fillCurrentEventData,
  setupEventOnServer,
} from "../../../../redux/actions";

export const CreateNewEventStepTwo = () => {
  const [document, setDocument] = useState(null);
  const [serverImage, setServerImage] = useState(null);
  const [imageMistake, setImageMistake] = useState("");
  const auth = useSelector((state) => state.auth.authorised);
  const currentEvent = useSelector((state) => state.crEvent.currentEvent);
  const events = useSelector((state) => state.crEvent.events);
  const token = useSelector((state) => state.user.token);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!currentEvent) history.push("/create-new-event/step-1");
  });

  useEffect(() => {
    if (events.length > 0) history.push("/event-hall");
  }, [events]);

  useEffect(() => {
    if (
      !auth &&
      currentEvent &&
      currentEvent.username &&
      currentEvent.newPassword &&
      currentEvent.idPhoto
    ) {
      dispatch(createUser(currentEvent));
    }
  }, [auth, currentEvent, dispatch]);

  const handleCreateEvent = useCallback(
    async (currentEvent, getToken) => {
      try {
        await dispatch(setupEventOnServer(currentEvent, getToken()));
      } catch (e) {
        console.log(e.message);
      }
    },
    [dispatch]
  );

  useEffect(() => {
    if (
      auth &&
      currentEvent &&
      token &&
      currentEvent.eventDate &&
      currentEvent.eventType &&
      currentEvent.eventName
    ) {
      handleCreateEvent(currentEvent, getToken);
    }
  }, [auth, handleCreateEvent, currentEvent, token]);

  useEffect(() => {
    if (serverImage) setImageMistake("");
  }, [serverImage, imageMistake]);

  useEffect(() => {
    if (document) uploadPhoto(document, setServerImage);
  }, [document]);

  return (
    <div className={"event-page-2-container"}>
      <Hero imgSrc="/img/bg-Create-new-Event-01.jpg">
        <InnerHero />
      </Hero>

      <Formik
        initialValues={stepTwoValues}
        validate={(values) => {
          const errors = {};

          switch (true) {
            case !values.newPassword:
              errors.newPassword = "שדה חובה*";
              return errors;
            case !emailValidator(values.newPassword):
              errors.newPassword = "חסרים נתונים";
              return errors;
            case !values.confirmPassword:
              errors.confirmPassword = "שדה חובה*";
              return errors;
            case values.confirmPassword !== values.newPassword:
              errors.confirmPassword = "אופס!  אימות הסיסמא נכשל, נסה שו ב";
              return errors;
            case !values.isRulesRead:
              errors.isRulesRead = "יש לאשר את התקנו*";
              return errors;
            case !values.idCard:
              errors.idCard = "שדה חובה*";
              return errors;
            case !values.bank:
              errors.bank = "שדה חובה*";
              return errors;
            case !values.branch:
              errors.branch = "שדה חובה*";
              return errors;
            case !values.accountNumber:
              errors.accountNumber = "שדה חובה*";
              return errors;
            case !values.accountOwnerName:
              errors.accountOwnerName = "שדה חובה*";
              return errors;
            case !isValidIsraeliID(values.idCard):
              errors.idCard = "מס' ת.ז. שגו י";
              return errors;
            case !validator(values.bank, values.branch, values.accountNumber):
              errors.bank = "פרטי חשבון אינם תקינים";
              return errors;
            default:
              return errors;
          }
        }}
        onSubmit={(values, { setSubmitting }) => {
          if (serverImage) {
            setSubmitting(false);
            dispatch(
              fillCurrentEventData({
                ...values,
                idPhoto: serverImage || defaultImage,
              })
            );
          } else {
            setSubmitting(false);
            setTimeout(() => setSubmitting(false), 100);
            setImageMistake("נדרש לצרף צילום המחאה לצורך אימות החשבו");
          }
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue,
        }) => (
          <form onSubmit={handleSubmit}>
            <section id="services">
              <div className="container">
                <div className="row">
                  <div className="div-title text-center mt-md-5 mt-sm-4 mt-3">
                    <div className="heading-overline " />
                    <div className="title-desc mt-3">
                      <h3>פרטי כניסה</h3>
                    </div>
                  </div>

                  <div className="col-md-2 hide-sm hide-xs" />
                  <div className="col-md-8 col-sm-12 col-12 password-container">
                    <div className="from-wrapper pr-md-4 pr-sm-4">
                      <div className="form-group text-right d-inline-block mt-5">
                        <label htmlFor="confirmPassword">אישור סיסמא</label>
                        <input
                          type="password"
                          autoComplete="confirm-password"
                          name="confirmPassword"
                          className={classNames(
                            "form-control fc",
                            errors.confirmPassword &&
                              touched.confirmPassword &&
                              errors.confirmPassword &&
                              "fc-wrong"
                          )}
                          value={values.confirmPassword}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.confirmPassword &&
                            touched.confirmPassword &&
                            errors.confirmPassword
                          }
                        />
                      </div>
                    </div>

                    <div className="from-wrapper pl-md-4 pl-sm-4">
                      <div className="form-group text-right d-inline-block mt-5 position-relative">
                        <span
                          className={classNames(
                            "new-password-rules",
                            errors.newPassword &&
                              touched.newPassword &&
                              errors.newPassword &&
                              "new-password-rules-active"
                          )}
                        >
                          הסיסמא חייבת להכיל 2 אותיות ו-4 מספרים
                        </span>
                        <label htmlFor="newPassword">סיסמא</label>
                        <input
                          autoComplete="new-password"
                          type="password"
                          name="newPassword"
                          className={classNames(
                            "form-control fc",
                            ((errors.newPassword &&
                              touched.newPassword &&
                              errors.newPassword) ||
                              (errors.confirmPassword &&
                                touched.confirmPassword &&
                                errors.confirmPassword)) &&
                              "fc-wrong"
                          )}
                          value={values.newPassword}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.newPassword &&
                            touched.newPassword &&
                            errors.newPassword
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-2 hide-sm hide-xs" />
                </div>

                <div className="row">
                  <div className="col-md-2 hide-sm hide-xs" />
                  <div className="col-md-8 col-sm-12 col-12">
                    <div className="service-desc text-right pt-0 rules-was-not-read">
                      <div className="form-agree">
                        <div>
                          <h3 className="text-right">
                            אני מאשר\ת כי קראתי ואני מסכים לתנאי
                            <span className="fn-b"> תקנון חברת גיבר בע"מ</span>
                          </h3>
                        </div>
                        <div>
                          <input
                            type="checkbox"
                            className="check ml-1"
                            value={values.isRulesRead}
                            checked={values.isRulesRead}
                            name="isRulesRead"
                            onChange={handleChange}
                          />
                        </div>
                      </div>
                      <ValidationMistakeText
                        mistake={
                          errors.isRulesRead &&
                          touched.isRulesRead &&
                          errors.isRulesRead
                        }
                        absolute
                      />
                    </div>
                  </div>
                  <div className="col-md-2 hide-xs" />
                </div>
              </div>
            </section>

            <section id="services">
              <div className="container">
                <div className="row">
                  <div className="div-title text-center mt-5 mb-md-5">
                    <div className="heading-overline mt-md-5 mt-sm-4 mt-3 mb-3" />
                    <div className="title-desc">
                      <h3>פרטי זיכוי</h3>
                    </div>
                  </div>
                </div>

                <div className={"row"}>
                  <div
                    className={"col-md-12 col-sm-12 col-12 bank-data-container"}
                  >
                    <div className="from-wrapper pr-md-3">
                      <div className="form-group text-right d-inline-block mt-5 branch-wrong position-relative">
                        <label htmlFor="branch">מספר סניף</label>
                        <CustomSelect
                          name={"branch"}
                          handler={(field, value) =>
                            setFieldValue(field, value)
                          }
                          mistake={
                            (errors.bank && touched.bank && errors.bank) ||
                            (errors.branch && touched.branch && errors.branch)
                          }
                          bankSet={values.bank}
                          disabled={!values.bank}
                          value={values.branch}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.branch && touched.branch && errors.branch
                          }
                          absolute
                        />
                      </div>
                    </div>

                    <div className="from-wrapper pl-md-3 pr-md-3">
                      <div className="form-group text-right d-inline-block mt-5 position-relative bank-wrong">
                        <label htmlFor="bank">שם בנק</label>
                        <CustomSelect
                          name={"bank"}
                          handler={(field, value) =>
                            setFieldValue(field, value)
                          }
                          mistake={errors.bank && touched.bank && errors.bank}
                          value={values.bank}
                        />
                        <ValidationMistakeText
                          mistake={errors.bank && touched.bank && errors.bank}
                          absolute
                        />
                      </div>
                    </div>

                    <div className="from-wrapper pl-md-3">
                      <div className="form-group text-right d-inline-block mt-5">
                        <label htmlFor="idCard">תעודת זהות</label>
                        <input
                          type="text"
                          name="idCard"
                          className={classNames(
                            "form-control fc",
                            errors.idCard &&
                              touched.idCard &&
                              errors.idCard &&
                              "fc-wrong"
                          )}
                          value={values.idCard}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.idCard && touched.idCard && errors.idCard
                          }
                        />
                        {/*339677395    example of correct ID number to pass validation*/}
                      </div>
                    </div>
                  </div>
                </div>

                <div className="row">
                  <div
                    className={
                      "col-md-12 col-sm-12 col-12 user-payment-container"
                    }
                    id="user-payment"
                  >
                    <div className="from-wrapper pr-md-3">
                      <div className="form-group camera mt-4">
                        <div
                          className={classNames(
                            "add-file",
                            imageMistake && "add-file-not-found"
                          )}
                        >
                          <label className="label">
                            {serverImage ? (
                              <i className="fas fa-check-square" />
                            ) : (
                              <i className="fas fa-camera fa-3x" />
                            )}
                            <input
                              type="file"
                              name="eventImage"
                              onChange={(e) =>
                                setDocument({ file: e.target.files[0] })
                              }
                            />
                          </label>
                        </div>
                        <label
                          htmlFor="firstName"
                          className={classNames(
                            "",
                            imageMistake && "mistake-label-camera"
                          )}
                        >
                          שיק לאימות החשבון
                        </label>
                        {imageMistake ? (
                          <ValidationMistakeText mistake={imageMistake} />
                        ) : null}
                      </div>
                    </div>

                    <div className="from-wrapper pl-md-3 pr-md-3">
                      <div className="form-group text-right d-inline-block mt-5">
                        <label htmlFor="accountOwnerName">שם בעל החשבון</label>
                        <input
                          type="text"
                          name="accountOwnerName"
                          className={classNames(
                            "form-control fc",
                            errors.accountOwnerName &&
                              touched.accountOwnerName &&
                              errors.accountOwnerName &&
                              "fc-wrong"
                          )}
                          value={values.accountOwnerName}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.accountOwnerName &&
                            touched.accountOwnerName &&
                            errors.accountOwnerName
                          }
                        />
                      </div>
                    </div>

                    <div className="from-wrapper pl-md-3">
                      <div className="form-group text-right d-inline-block mt-5">
                        <label htmlFor="accountNumber">מספר חשבון</label>
                        <input
                          type="text"
                          name="accountNumber"
                          className={classNames(
                            "form-control fc",
                            ((errors.bank && touched.bank && errors.bank) ||
                              (errors.accountNumber &&
                                touched.accountNumber &&
                                errors.accountNumber)) &&
                              "fc-wrong"
                          )}
                          value={values.accountNumber}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          disabled={!values.bank}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.accountNumber &&
                            touched.accountNumber &&
                            errors.accountNumber
                          }
                        />
                      </div>
                    </div>
                  </div>
                </div>

                <div className="row m-service camera-block">
                  <div className="col-md-12">
                    <div className="service-desc text-right centrify">
                      <img
                        src="/img/logo.png"
                        alt="logo"
                        className={"hide-xs hide-sm"}
                      />
                      <h3>יש לצלם שיק מבוטל לצורך הוכחת בעלות על החשבון</h3>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section id="link">
              <div className="container">
                <div className="row">
                  <div className="col-md-12 mt-3">
                    <div className="title-outer">
                      <div className="title-inner-1" />
                      <div className="title-inner-1" />
                    </div>
                    <div className="col-md-12 d-flex justify-content-center">
                      <div className="search-btn text-center">
                        <button
                          type="submit"
                          className="btn btn-lg event-2 mt-3 mt"
                          disabled={isSubmitting}
                        >
                          !סיימתי
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </form>
        )}
      </Formik>
      <Footer />
    </div>
  );
};

export default CreateNewEventStepTwo;
