import {
  REGISTER_USER_EMAIL,
  REGISTER_USER_NAME,
  REGISTER_USER_TOKEN,
  REGISTER_USER_ID,
  REGISTER_USER_BANK_DATA,
  REGISTER_USER_PASSWORD,
  REGISTER_SECURITY_CODE,
} from "../constants";
import { api } from "../../../core/api";
import { endpoint } from "../../../config";
import { setItemLs, setToken } from "../../../core/api/login";
import { setAuth } from "./authentication";
import { toggleCreateUserModal } from "./modal";

export const registerUserEmail = (email) => {
  return {
    type: REGISTER_USER_EMAIL,
    payload: email,
  };
};

export const registerUserPassword = (password) => {
  return {
    type: REGISTER_USER_PASSWORD,
    payload: password,
  };
};

export const registerUserName = (username) => {
  return {
    type: REGISTER_USER_NAME,
    payload: username,
  };
};

export const registerUserToken = (token) => {
  return {
    type: REGISTER_USER_TOKEN,
    payload: token,
  };
};

export const registerUserId = (id) => {
  return {
    type: REGISTER_USER_ID,
    payload: id,
  };
};

export const registerUserBankData = (obj) => {
  return {
    type: REGISTER_USER_BANK_DATA,
    payload: obj,
  };
};

export const registerSecurityCode = (string) => {
  return {
    type: REGISTER_SECURITY_CODE,
    payload: string,
  };
};

export const ifUserExist = (email) => {
  return api.post(endpoint.ifUserExist, { email });
};

export const ifEmailExist = (email) => {
  return api.post(endpoint.forgotPassword, { email });
};

export const createUser = (userData) => (dispatch) => {
  api
    .post(endpoint.createUser, {
      password: userData.newPassword,
      fullname: userData.username,
      email: userData.email,
      phone: userData.phoneNumber,
      acc_type: "ordinary",
      bank: {
        ac_no: +userData.accountNumber,
        holder_name: userData.accountOwnerName,
        bank_name: String(userData.bank),
        branch: String(userData.branch),
      },
      id_card_number: +userData.idCard,
      id_card_photo: userData.idPhoto,
    })
    .then((result) => {
      dispatch(setAuth());
      dispatch(registerUserName(userData.username));
      dispatch(registerUserEmail(userData.email));
      dispatch(registerUserPassword(userData.newPassword));
      dispatch(registerUserToken(result.token));
      dispatch(registerUserId(result.user._id));
      setTimeout(() => {
        dispatch(toggleCreateUserModal());
      }, 200);
      setToken(result.token);
      setItemLs("email", userData.email);
      setItemLs("password", userData.newPassword);
      setItemLs("username", result.user.fullname);
      setItemLs("id", result.user._id);
    })
    .catch((error) => console.log("error", error));
};

export const getUserInfo = (token) => (dispatch) => {
  api.get(endpoint.userProfile, token).then((res) => {
    dispatch(registerUserBankData(res));
  });
};
