import React from "react";
import { Formik } from "formik";
import { InnerHero, marketerCreateInitials } from "../index";
import { Footer, Hero, ValidationMistakeText } from "../../../common";
import classNames from "classnames";

export const MarketerCreate = () => {
  return (
    <>
      <Hero imgSrc="/img/bg-Create-new-Event-01.jpg">
        <InnerHero />
      </Hero>
      <Formik
        initialValues={marketerCreateInitials}
        validate={(values) => {
          const errors = {};

          switch (true) {
            case !values.affiliateName:
              errors.affiliateName = "שדה חובה*";
              return errors;
            case !values.affiliateMobile:
              errors.affiliateMobile = "שדה חובה*";
              return errors;
            case !values.affiliateEmail:
              errors.affiliateEmail = "שדה חובה*";
              return errors;
            case !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(
              values.affiliateEmail
            ):
              errors.affiliateEmail = 'פורמט דוא"ל לא חוקי';
              return errors;
            case !values.affiliateCompanyName:
              errors.affiliateCompanyName = "שדה חובה*";
              return errors;
            case !values.affiliateRole:
              errors.affiliateRole = "שדה חובה*";
              return errors;
            case !values.affiliateOfficePhone:
              errors.affiliateOfficePhone = "שדה חובה*";
              return errors;
            case !values.affiliateNewPassword:
              errors.affiliateNewPassword = "שדה חובה*";
              return errors;
            case !values.affiliateConfirmPassword:
              errors.affiliateConfirmPassword = "שדה חובה*";
              return errors;
            case values.affiliateConfirmPassword !==
              values.affiliateNewPassword:
              errors.affiliateConfirmPassword = "סיסמאות לא תואמות";
              return errors;
            default:
              return errors;
          }
        }}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(false);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
        }) => (
          <form onSubmit={handleSubmit}>
            <div className="container">
              <div className={"col-md-12"}>
                <div className="row">
                  <div className="div-title text-center">
                    <div className="heading-overline mt-md-5 mt-sm-4 mt-3 mb-3" />
                    <div className="title-desc">
                      <h3>שמחים להכיר</h3>
                    </div>
                  </div>
                </div>

                <div className={"row mt-65 fb-right"}>
                  <div className={"col-md-2 hide-sm hide-xs"} />
                  <div className="col-md-4 col-sm-6 col-6 text-right">
                    <a href="#/" className="btn btn-lg btn-google">
                      <i className="fab fa-google" />
                      <span className="btn-fb">הרשם דרך גוגל</span>
                    </a>
                  </div>
                  <div className="col-md-4 col-sm-6 col-6 text-left">
                    <a href="#/" className="btn btn-lg btn-facebook">
                      <i className="fab fa-facebook-f" />
                      <span className="btn-fb">הרשם דרך פייסבוק</span>
                    </a>
                  </div>
                  <div className={"col-md-2 hide-sm hide-xs"} />
                </div>

                <div className={"row mt-100 marketer-input-container"}>
                  <div className="form-group text-right col-md-4 col-sm-6 col-6">
                    <label htmlFor="affiliateName">שם מלא</label>
                    <input
                      type="text"
                      name="affiliateName"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateName &&
                          touched.affiliateName &&
                          errors.affiliateName &&
                          "fc-wrong"
                      )}
                      onChange={handleChange}
                      value={values.affiliateName}
                      onBlur={handleBlur}
                    />
                    <ValidationMistakeText
                      mistake={
                        errors.affiliateName &&
                        touched.affiliateName &&
                        errors.affiliateName
                      }
                      absolute
                    />
                  </div>
                  <div className="form-group text-right col-md-4 col-sm-6 col-6 ">
                    <label htmlFor="affiliateMobile">מספר נייד</label>
                    <input
                      type="text"
                      name="affiliateMobile"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateMobile &&
                          touched.affiliateMobile &&
                          errors.affiliateMobile &&
                          "fc-wrong"
                      )}
                      onChange={handleChange}
                      value={values.affiliateMobile}
                      onBlur={handleBlur}
                    />
                    <span className={"validation-marketer"}>
                      {errors.affiliateMobile &&
                        touched.affiliateMobile &&
                        errors.affiliateMobile}
                    </span>
                  </div>
                  <div className="form-group text-right col-md-4 col-sm-6 col-6 mt-sm-3 mt-3 mt-md-0">
                    <label htmlFor="affiliateEmail">דוא“ל</label>
                    <input
                      type="text"
                      name="affiliateEmail"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateEmail &&
                          touched.affiliateEmail &&
                          errors.affiliateEmail &&
                          "fc-wrong"
                      )}
                      onChange={handleChange}
                      value={values.affiliateEmail}
                      onBlur={handleBlur}
                    />
                    <ValidationMistakeText
                      mistake={
                        errors.affiliateEmail &&
                        touched.affiliateEmail &&
                        errors.affiliateEmail
                      }
                      absolute
                    />
                  </div>

                  <div className="form-group text-right col-md-4 col-sm-6 col-6 mt-sm-3 mt-3 mt-md-3">
                    <label htmlFor="affiliateCompanyName">שם חברה</label>
                    <input
                      type="text"
                      name="affiliateCompanyName"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateCompanyName &&
                          touched.affiliateCompanyName &&
                          errors.affiliateCompanyName &&
                          "fc-wrong"
                      )}
                      onChange={handleChange}
                      value={values.affiliateCompanyName}
                      onBlur={handleBlur}
                    />
                    <span className={"validation-marketer"}>
                      {errors.affiliateCompanyName &&
                        touched.affiliateCompanyName &&
                        errors.affiliateCompanyName}
                    </span>
                  </div>
                  <div className="form-group text-right col-md-4 col-sm-6 col-6 mt-sm-3 mt-3 mt-md-3">
                    <label htmlFor="affiliateRole">תפקיד</label>
                    <input
                      type="text"
                      name="affiliateRole"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateRole &&
                          touched.affiliateRole &&
                          errors.affiliateRole &&
                          "fc-wrong"
                      )}
                      onChange={handleChange}
                      value={values.affiliateRole}
                      onBlur={handleBlur}
                    />
                    <ValidationMistakeText
                      mistake={
                        errors.affiliateRole &&
                        touched.affiliateRole &&
                        errors.affiliateRole
                      }
                      absolute
                    />
                  </div>
                  <div className="form-group text-right col-md-4 col-sm-6 col-6 mt-sm-3 mt-3 mt-md-3">
                    <label htmlFor="affiliateOfficePhone">טלפון משרד</label>
                    <input
                      type="text"
                      name="affiliateOfficePhone"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateOfficePhone &&
                          touched.affiliateOfficePhone &&
                          errors.affiliateOfficePhone &&
                          "fc-wrong"
                      )}
                      onChange={handleChange}
                      value={values.affiliateOfficePhone}
                      onBlur={handleBlur}
                    />
                    <ValidationMistakeText
                      mistake={
                        errors.affiliateOfficePhone &&
                        touched.affiliateOfficePhone &&
                        errors.affiliateOfficePhone
                      }
                      absolute
                    />
                  </div>

                  <div className="form-group text-right col-md-4 col-sm-6 col-6 mt-sm-3 mt-3 mt-md-3">
                    <label htmlFor="affiliateNewPassword">סיסמא</label>
                    <input
                      type="password"
                      name="affiliateNewPassword"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateNewPassword &&
                          touched.affiliateNewPassword &&
                          errors.affiliateNewPassword &&
                          "fc-wrong"
                      )}
                      autoComplete={"affiliateNewPassword"}
                      onChange={handleChange}
                      value={values.affiliateNewPassword}
                      onBlur={handleBlur}
                    />
                    <ValidationMistakeText
                      mistake={
                        errors.affiliateNewPassword &&
                        touched.affiliateNewPassword &&
                        errors.affiliateNewPassword
                      }
                      absolute
                    />
                  </div>
                  <div className="form-group text-right col-md-4 col-sm-6 col-6 mt-sm-3 mt-3 mt-md-3">
                    <label htmlFor="affiliateConfirmPassword">אמות סיסמא</label>
                    <input
                      type="password"
                      name="affiliateConfirmPassword"
                      className={classNames(
                        "form-control fc",
                        errors.affiliateConfirmPassword &&
                          touched.affiliateConfirmPassword &&
                          errors.affiliateConfirmPassword &&
                          "fc-wrong"
                      )}
                      autoComplete={"false"}
                      onChange={handleChange}
                      value={values.affiliateConfirmPassword}
                      onBlur={handleBlur}
                    />
                    <ValidationMistakeText
                      mistake={
                        errors.affiliateConfirmPassword &&
                        touched.affiliateConfirmPassword &&
                        errors.affiliateConfirmPassword
                      }
                      absolute
                    />
                  </div>
                  <div
                    className="form-group text-right col-md-4 hide-sm hide-xs"
                    style={{ opacity: "0" }}
                  />
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <div className="service-desc pt-0">
                      <div className={"form-agree-marketer"}>
                        <div>
                          <h3>
                            אני מאשרת כי קראתי ואני מסכים לתנאי
                            <span className={"fn-b"}>
                              {" "}
                              תקנון חברת ניבר בע"מ
                            </span>
                          </h3>
                        </div>
                        <div>
                          <input type={"checkbox"} className={"check ml-1"} />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="row mt-5">
                  <div className="col-md-12">
                    <div className="link-btn text-center">
                      <button
                        type="submit"
                        className="marketer-form-button"
                        disabled={isSubmitting}
                      >
                        צור משתמש
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </form>
        )}
      </Formik>
      <Footer />
    </>
  );
};

export default MarketerCreate;
