export const contactFormInitials = {
  nameContact: "",
  emailContact: "",
  messageContact: "",
  messageContactTwo: "",
};

export const dateFormatter = (date) => {
  let d = new Date(date),
    month = "" + (d.getMonth() + 1),
    day = "" + d.getDate(),
    year = d.getFullYear();

  if (month.length < 2) month = "0" + month;
  if (day.length < 2) day = "0" + day;

  return [year, month, day].join("-");
};

export const phoneMinusParser = (number) => {
  const numberToArray = number.trim().split("");
  const numbers = [];

  for (let i = 0; i < numberToArray.length; i += 1) {
    if (isNaN(numberToArray[i] / 1)) return false;
    else numbers.push(+numberToArray[i]);
  }

  return number.length === 3;
};

export const strCutter = (str) => {
  return str.split("-")[0].split("").splice(0, 2).join("");
};

export const nameFormatter = (name) => {
  const nameToArray = name.trim().split("");
  const literals = [];

  for (let i = 0; i < nameToArray.length; i += 1) {
    if (isNaN(nameToArray[i] / 1) || nameToArray[i] === " ")
      literals.push(nameToArray[i]);
    else return false;
  }

  if (!literals.includes(" ")) return false;
  else if (literals.indexOf(" ") < 2) return false;
  else if (literals.length - literals.indexOf(" ") <= 2) return false;
  return true;
};

export const phoneNumberFormatter = (phone) => {
  const nameToArray = phone.trim().split("");
  const numbers = [];

  for (let i = 0; i < nameToArray.length; i += 1) {
    if (!isNaN(nameToArray[i] / 1)) numbers.push(+nameToArray[i]);
    else if (nameToArray[i] === "-") numbers.push(nameToArray[i]);
    else return false;
  }

  if (numbers.length < 11 || numbers.length > 11) return false;
  else if (numbers[0] !== 0 && numbers[1] !== 5 && numbers[3] === "-")
    return false;
  return true;
};

export const messageFormatter = (message) => {
  const nameToArray = message.trim().split("");
  const numbers = [];
  const literals = [];

  for (let i = 0; i < nameToArray.length; i += 1) {
    if (isNaN(nameToArray[i] / 1) || nameToArray[i] === " ")
      literals.push(nameToArray[i]);
    else numbers.push(+nameToArray[i]);
  }

  if (!literals.includes(" ")) return false;
  else if (literals.indexOf(" ") < 2) return false;
  else if (literals.length - literals.indexOf(" ") <= 2) return false;
  return true;
};
