import React from "react";
import { NavLink } from "react-router-dom";
import { contactActivated } from "../../../redux/actions";
import { useDispatch } from "react-redux";

export const HeaderLink = ({ linkSettings }) => {
  const { activeClassName, to, text } = linkSettings;
  const dispatch = useDispatch();

  let handleContact = async () => {
    await dispatch(contactActivated());
    setTimeout(() => {
      dispatch(contactActivated());
    }, 500);
  };

  if (text === "giver") handleContact = null;

  return (
    <li className="nav-item">
      <NavLink
        className="nav-link"
        activeClassName={activeClassName}
        to={to}
        onClick={handleContact}
      >
        {text}
      </NavLink>
    </li>
  );
};

export default HeaderLink;
