import React from "react";
import { Formik } from "formik";

import { condition } from "../../views/CreateNewEvent";
import classNames from "classnames";
import { ValidationMistakeText } from "../index";
import { toggleAffiliateLogin } from "../../../redux/actions";
import { useDispatch } from "react-redux";

export function LoginAffiliate() {
  const dispatch = useDispatch();
  return (
    <div className={"login-affiliate-container hide-xs"}>
      <div
        className={"login-affiliate__close"}
        onClick={() => dispatch(toggleAffiliateLogin())}
      >
        X
      </div>
      <Formik
        initialValues={{
          affiliateName: "",
          affiliatePassword: "",
        }}
        validate={(values) => {
          const errors = {};
          condition(values, errors);
          return errors;
        }}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(false);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
        }) => (
          <form onSubmit={handleSubmit}>
            <div className={"login-affiliate__body"}>
              <div className="form-group text-right">
                <label htmlFor="affiliateName">מייל</label>
                <input
                  type="text"
                  name="affiliateName"
                  className={classNames(
                    "form-control fc",
                    errors.affiliateName &&
                      touched.affiliateName &&
                      errors.affiliateName &&
                      "fc-wrong"
                  )}
                  value={values.affiliateName}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <ValidationMistakeText
                  absolute
                  mistake={
                    errors.affiliateName &&
                    touched.affiliateName &&
                    errors.affiliateName
                  }
                />
              </div>
              <div className="form-group text-right">
                <label htmlFor="affiliatePassword">סיסמא</label>
                <input
                  type="password"
                  name="affiliatePassword"
                  value={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  autoComplete={"password"}
                  className={classNames(
                    "form-control fc",
                    errors.affiliatePassword &&
                      touched.affiliatePassword &&
                      errors.affiliatePassword &&
                      "fc-wrong"
                  )}
                />
                <ValidationMistakeText
                  absolute
                  mistake={
                    errors.affiliatePassword &&
                    touched.affiliatePassword &&
                    errors.affiliatePassword
                  }
                />
              </div>
            </div>
            <div className={"login-affiliate__footer"}>
              <div className={"login-affiliate__footer__login-btn"}>
                <button type="submit" disabled={isSubmitting}>
                  התחבר
                </button>
              </div>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
}

export default LoginAffiliate;
