import {
  REGISTER_USER_EMAIL,
  REGISTER_USER_NAME,
  REGISTER_USER_TOKEN,
  REGISTER_USER_ID,
  REGISTER_USER_BANK_DATA,
  REGISTER_USER_PASSWORD,
  REGISTER_SECURITY_CODE,
} from "../constants";

const initialState = {
  username: "",
  email: "",
  token: "",
  password: "",
  id: "",
  bank: {},
  idCardNumber: 0,
  idCardPhoto: "",
  guestSecurityCode: "",
};

export const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case REGISTER_USER_EMAIL:
      return { ...state, email: action.payload };
    case REGISTER_USER_PASSWORD:
      return { ...state, password: action.payload };
    case REGISTER_USER_NAME:
      return { ...state, username: action.payload };
    case REGISTER_USER_TOKEN:
      return { ...state, token: action.payload };
    case REGISTER_USER_ID:
      return { ...state, id: action.payload };
    case REGISTER_USER_BANK_DATA:
      return {
        ...state,
        bank: action.payload.bank,
        idCardNumber: action.payload.id_card_number,
        idCardPhoto: action.payload.id_card_photo,
      };
    case REGISTER_SECURITY_CODE: {
      return {
        ...state,
        guestSecurityCode: action.payload,
      };
    }
    default:
      return state;
  }
};

export default userReducer;
