import React, { useCallback, useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getEventsFromServer, getUserInfo } from "../../../redux/actions";
import { Footer, Hero, PopupUserCreated } from "../../common";
import { InnerHero, EventDetails } from "./index";
import PaginationItem from "../sendMoney/components/PaginationItem";
import { getToken } from "../../../../core/api/login";

export function EventHall() {
  const dispatch = useDispatch();
  const auth = useSelector((state) => state.auth.authorised);
  const events = useSelector((state) => state.crEvent.events);
  const user = useSelector((state) => state.user);
  const createUserModal = useSelector((state) => state.modal.createUserModal);
  const [selectedEvent, setSelectedEvent] = useState("");

  const bankData = useMemo(() => {
    return {
      ac_no: user.bank.ac_no,
      bank_name: user.bank.bank_name,
      branch: user.bank.branch,
    };
  }, [user]);

  const handleGetEvents = useCallback(async () => {
    try {
      await dispatch(getEventsFromServer(getToken()));
      await dispatch(getUserInfo(getToken()));
    } catch (e) {
      console.log(e.message);
    }
  }, [dispatch]);

  useEffect(() => {
    if (auth && events.length < 1) handleGetEvents();
  }, [auth, events.length, handleGetEvents]);

  const lastFiveEvents = useMemo(
    () =>
      events.filter((_, index, array) => index >= array.length - 5).reverse(),
    [events]
  );

  useEffect(() => {
    if (lastFiveEvents.length > 0) setSelectedEvent(lastFiveEvents[0]);
  }, [lastFiveEvents]);

  console.log(selectedEvent);

  const gifts = [650, 650, 100, 800, 1000, 1500, 300, 2000]; //temp for example, there should be gifts from server
  const eventDetails = {
    stats: {
      left: 3,
      right: 81,
    },
    gifts,
  };

  const handleId = (id) => {
    setSelectedEvent((prevState) => lastFiveEvents.find((el) => el._id === id));
  };

  return (
    <>
      <Hero imgSrc="/img/Even-page-(user-view)-01.jpg">
        <InnerHero />
      </Hero>
      {createUserModal ? <PopupUserCreated /> : null}
      <section id="services">
        <div className="container">
          <div className="row">
            <div className="div-title text-center">
              <div className="heading-overline mt-65 mb-3" />
              <div className="title-desc">
                <h3>אירועים אחרונים</h3>
              </div>
            </div>
          </div>
          <div className={"row"}>
            {lastFiveEvents.map((item) => (
              <PaginationItem
                key={"event" + Math.random() + item.eventName}
                eventName={item.title}
                selected={selectedEvent._id}
                handleId={handleId}
                eventDetails={{
                  date: item.date,
                  venue: item.venue,
                  id: item._id,
                  image: item.thumbnail,
                }}
              />
            ))}
          </div>
        </div>
      </section>
      <EventDetails details={eventDetails} bankData={bankData} />
      <Footer />
    </>
  );
}

export default EventHall;
