import React, { useRef } from "react";
import { toggleUserExistModal } from "../../../redux/actions";
import { useDispatch } from "react-redux";
import { useOnClickOutside } from "..";

export const PopupUserExist = () => {
  const dispatch = useDispatch();
  const ref = useRef();
  useOnClickOutside(ref, () => dispatch(toggleUserExistModal()));

  return (
    <div className={"popup-modal"}>
      <div className={"popup-user-exist popup-general"} ref={ref}>
        <div
          className={"popup-close"}
          onClick={() => dispatch(toggleUserExistModal())}
        >
          &times;
        </div>
        <span>משתמש כבר קיים. יש להתחבר לחשבונך לפני יצירת אירוע חדש</span>
      </div>
    </div>
  );
};

export default PopupUserExist;
