import React, { useEffect } from "react";

export const ValidationMistakeText = ({ mistake, absolute }) => {
  const ref = React.createRef();
  useEffect(() => {
    if (mistake)
      ref.current.scrollIntoView({ block: "center", behavior: "smooth" });
  });
  if (!mistake) return null;

  return (
    <span
      className={!absolute ? "validation-mistake-span" : "validation-marketer"}
      ref={ref}
    >
      {mistake}
    </span>
  );
};

export default ValidationMistakeText;
