import React, { useCallback, useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Formik } from "formik";
import classNames from "classnames";

import { api } from "../../../../core/api";
import { endpoint } from "../../../../config";
import {
  contactFormInitials,
  ValidationMistakeText,
  nameFormatter,
  messageFormatter,
  PopupContactForm,
} from "../index";
import { toggleModalContact } from "../../../redux/actions";

function useOnClickOutside(ref, handler, callback) {
  useEffect(() => {
    const listener = (event) => {
      if (!ref.current || ref.current.contains(event.target)) handler(event);
      else {
        callback();
      }
    };

    document.addEventListener("mousedown", listener);
    document.addEventListener("touchstart", listener);

    return () => {
      document.removeEventListener("mousedown", listener);
      document.removeEventListener("touchstart", listener);
    };
  }, [ref, handler, callback]);
}

export const ContactForm = () => {
  const [isContactFormClicked, setContactFormClicked] = useState(false);
  const modalContact = useSelector((state) => state.modal.modalContact);
  const dispatch = useDispatch();
  const ref = useRef();

  useOnClickOutside(
    ref,
    () => setContactFormClicked(true),
    () => setContactFormClicked(false)
  );

  const handleSendFormData = useCallback(
    async (obj) => {
      try {
        await api
          .post(endpoint.contact, {
            name: obj.nameContact,
            email: obj.emailContact,
            message: obj.messageContact + " " + obj.messageContactTwo,
          })
          .then((res) => {
            console.log(res);
            dispatch(toggleModalContact());
          });
      } catch (e) {
        console.log(e.message);
      }
    },
    [dispatch]
  );

  return (
    <section id="contact" ref={ref}>
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="contact-wrapper clearfix mb-5 position-relative">
              {modalContact ? (
                <PopupContactForm hide={() => dispatch(toggleModalContact())} />
              ) : null}
              <div className="div-title text-center">
                <div className="heading-overline mb-3" />
                <div className="title-desc">
                  <h3>.דברו איתנו, חופשי</h3>
                </div>
              </div>
            </div>

            <Formik
              initialValues={contactFormInitials}
              validate={(values) => {
                const errors = {};
                switch (true) {
                  case !values.nameContact:
                    errors.nameContact = "שדה חובה*";
                    return errors;
                  case !nameFormatter(values.nameContact):
                    errors.nameContact = "שם אינו תקי ן*";
                    return errors;
                  case !values.emailContact:
                    errors.emailContact = "שדה חובה*";
                    return errors;
                  case !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(
                    values.emailContact
                  ):
                    errors.emailContact = 'כתובת  דוא"ל אינה תקינ*';
                    return errors;
                  case !values.messageContact && errors.messageContactTwo:
                    errors.messageContact = "שדה חובה*";
                    return errors;
                  case !messageFormatter(
                    values.messageContact + " " + values.messageContactTwo
                  ):
                    errors.messageContact = "הודעה קצרה מיד י*";
                    return errors;
                  case !values.messageContactTwo && errors.messageContact:
                    errors.messageContactTwo = "שדה חובה*";
                    return errors;

                  default:
                    return errors;
                }
              }}
              onSubmit={async (values, { setSubmitting, resetForm }) => {
                await handleSendFormData(values);
                await resetForm();
                setSubmitting(false);
              }}
            >
              {({
                values,
                handleChange,
                errors,
                touched,
                handleSubmit,
                isSubmitting,
              }) => (
                <form className="contact-form" onSubmit={handleSubmit}>
                  <div className="row">
                    <div className={"contact-form-username"}>
                      <div className="form-group text-right mr-md-5 position-relative">
                        <label htmlFor="emailContact">דוא"ל</label>
                        <input
                          type="text"
                          name="emailContact"
                          className={classNames(
                            "form-control fc",
                            errors.emailContact &&
                              touched.emailContact &&
                              errors.emailContact &&
                              isContactFormClicked &&
                              "fc-wrong"
                          )}
                          onChange={handleChange}
                          value={values.emailContact}
                        />
                        {isContactFormClicked ? (
                          <ValidationMistakeText
                            mistake={
                              errors.emailContact &&
                              touched.emailContact &&
                              errors.emailContact
                            }
                            absolute
                          />
                        ) : null}
                      </div>

                      <div className="form-group text-right ml-md-5 position-relative">
                        <label htmlFor="nameContact">שם</label>
                        <input
                          type="text"
                          name="nameContact"
                          className={classNames(
                            "form-control fc",
                            errors.nameContact &&
                              touched.nameContact &&
                              errors.nameContact &&
                              isContactFormClicked &&
                              "fc-wrong"
                          )}
                          onChange={handleChange}
                          value={values.nameContact}
                        />
                        {isContactFormClicked ? (
                          <ValidationMistakeText
                            mistake={
                              errors.nameContact &&
                              touched.nameContact &&
                              errors.nameContact
                            }
                            absolute
                          />
                        ) : null}
                      </div>
                    </div>

                    <div className={"contact-footer mb-4"}>
                      <div className="form-group text-right mt-md-5 mt-sm-3 mt-0 position-relative contact-error">
                        <label htmlFor="messageContact">תוכן ההודעה</label>
                        <input
                          type="text"
                          name="messageContact"
                          className={classNames(
                            "form-control fc",
                            errors.messageContact &&
                              touched.messageContact &&
                              errors.messageContact &&
                              isContactFormClicked &&
                              "fc-wrong"
                          )}
                          onChange={handleChange}
                          value={values.messageContact}
                        />
                        <input
                          type="text"
                          name="messageContactTwo"
                          className={classNames(
                            "form-control fc mb-4",
                            ((errors.messageContactTwo &&
                              touched.messageContactTwo &&
                              errors.messageContactTwo) ||
                              (errors.messageContact &&
                                touched.messageContact &&
                                errors.messageContact)) &&
                              isContactFormClicked &&
                              "fc-wrong"
                          )}
                          onChange={handleChange}
                          value={values.messageContactTwo}
                        />
                        {isContactFormClicked ? (
                          <ValidationMistakeText
                            mistake={
                              errors.messageContact &&
                              touched.messageContact &&
                              errors.messageContact
                            }
                            absolute
                          />
                        ) : null}
                        <button
                          type={"submit"}
                          disabled={isSubmitting}
                          className="btn btn-dark hide-md hide-xl hide-lg contact-submit"
                        >
                          שלח
                        </button>
                      </div>
                      <button
                        type="submit"
                        className="btn btn-color btn-sz hide-sm hide-xs"
                        disabled={isSubmitting}
                      >
                        <i className="fas fa-paper-plane fa-3x" />
                      </button>
                    </div>
                  </div>
                </form>
              )}
            </Formik>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContactForm;
