import React, { useRef, useState } from "react";
import { useOnClickOutside } from "../index";
import { Formik } from "formik";
import { ifEmailExist } from "../../../redux/actions";

export const ForgotPasswordLogin = ({ hide }) => {
  const ref = useRef();
  useOnClickOutside(ref, () => hide());
  const [isEmailValid, setEmailValid] = useState(null);

  return (
    <div className={"forgot-password-container login-popup-general"} ref={ref}>
      <div className={"login-form__close hide-xs"} onClick={hide}>
        X
      </div>
      <Formik
        initialValues={{
          forgotPasswordEnter: "",
        }}
        onSubmit={async (values, { setSubmitting, resetForm }) => {
          try {
            const { succss } = await ifEmailExist(values.forgotPasswordEnter);
            if (succss) {
              setEmailValid(true);
            } else {
              setEmailValid(false);
              resetForm();
            }
          } catch (e) {
            console.log(e.error);
          }
          setSubmitting(false);
        }}
      >
        {({
          values,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          resetForm,
        }) => (
          <form onSubmit={handleSubmit}>
            <div className={"forgot-password-input position-relative"}>
              <div className="form-group text-right">
                <input
                  type="text"
                  name="forgotPasswordEnter"
                  value={values.forgotPasswordEnter}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={'כתובת דוא"ל'}
                  autoComplete={"forgotPasswordEnter"}
                  className={"form-control fc"}
                  disabled={isEmailValid}
                />
              </div>
              {isEmailValid === null ? null : isEmailValid ? (
                <span className={"forgot-password-input__validation-result"}>
                  הוראות שחרור הסיסמ א נשלחה לכתובת שהזנת
                  <i className="far fa-check-circle" />
                </span>
              ) : (
                <span className={"forgot-password-input__validation-result"}>
                  .אופס! הכתובת שהזנת לא נמצאת במערכת
                  <i className="far fa-times-circle" />
                </span>
              )}
            </div>
            <div className={"forgot-password-button"}>
              <button type="submit" disabled={isSubmitting}>
                אישור
              </button>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
};

export default ForgotPasswordLogin;
