import React, { useRef } from "react";
import { toggleFailedUserLogin } from "../../../redux/actions";
import { useDispatch } from "react-redux";
import { useOnClickOutside } from "..";

export const PopupLoginFailed = ({ text, nextTry }) => {
  const dispatch = useDispatch();
  const ref = useRef();
  useOnClickOutside(ref, () => dispatch(toggleFailedUserLogin()));

  const handleNextTry = async () => {
    await nextTry(true);
    await dispatch(toggleFailedUserLogin());
  };

  return (
    <div className={"popup-modal"}>
      <div className={"login-failed-popup popup-general"} ref={ref}>
        <div
          className={"popup-close"}
          onClick={() => dispatch(toggleFailedUserLogin())}
        >
          &times;
        </div>
        <div className={"login-failed-popup__text"}>
          <h3>{text}</h3>
        </div>
        <div className={"login-failed-popup__button"}>
          <button onClick={handleNextTry}>נסו שוב</button>
        </div>
      </div>
    </div>
  );
};

export default PopupLoginFailed;
