import React from "react";
import { useRouteMatch } from "react-router-dom";
import classNames from "classnames";
import moment from "moment";
import "moment/locale/he";
import { history } from "../../../../routes/AppRouter";

export const PaginationItem = ({
  eventName,
  eventDetails,
  handleId,
  selected,
}) => {
  const { date, venue, id, image } = eventDetails;
  let { url } = useRouteMatch();

  const formateDate = (date) => {
    const fullDate = moment(date).locale("he").format("LL");
    const day = moment(date).format("dddd");

    return `${fullDate} ${day}`;
  };

  return (
    <div
      className={classNames(
        "col-md-12 pagination-item",
        selected ? selected === id && "pagination-item-selected" : null
      )}
      onClick={() => {
        if (url === "/send-money") {
          history.push(`/event-page/${id}`);
        } else {
          handleId(id);
        }
      }}
    >
      <div className="row">
        <div className="col-md-2 col-sm-1 col-1 p-0 paim">
          <img src="img/play-gray.png" alt="" />
        </div>
        <div className="col-md-8 col-sm-8 col-11 bordered">
          <div className={"pagination-item-header mt-3"}>
            {url === "/event-hall" ? (
              <div className={"pagination-item-header__gifts"}>
                <h3>סיכום מתנות: 21,800</h3>
              </div>
            ) : null}
            <h3 className="fn-b">{eventName}</h3>
          </div>
          <div className={"pagination-item-footer"}>
            <h3 className={"mr-2"}>
              {formateDate(date) + " | " + (venue ? venue : "place")}
            </h3>
            <img src="img/location-gray.png" alt="" />
          </div>
        </div>
        <div className="col-md-2 col-sm-3 col-3 hide-xs p-0">
          <div className="pr">
            <img src={image} alt={""} className={"pagination-circle-img"} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default PaginationItem;
