import {
  ADD_EVENT_DATA,
  CREATE_EVENT,
  GET_EVENTS,
  CLEAR_CURRENT_EVENT,
} from "../constants";

const initialState = {
  events: [],
  currentEvent: undefined,
};

export const createEventReducer = (state = initialState, action) => {
  switch (action.type) {
    case CREATE_EVENT:
      return {
        ...state,
        events: [...state.events, action.payload],
      };
    case GET_EVENTS:
      return {
        ...state,
        events: action.payload,
      };
    case ADD_EVENT_DATA:
      return {
        ...state,
        currentEvent: { ...state.currentEvent, ...action.payload },
      };
    case CLEAR_CURRENT_EVENT:
      return {
        ...state,
        currentEvent: undefined,
      };
    default:
      return state;
  }
};

export default createEventReducer;
