import React, { useRef } from "react";
import { useOnClickOutside } from "..";

export const PopupContactForm = ({ hide }) => {
  const ref = useRef();
  useOnClickOutside(ref, () => hide());

  return (
    <div className={"popup-modal"}>
      <div className={"popup-contact popup-general"} ref={ref}>
        <div className={"popup-close"} onClick={hide}>
          &times;
        </div>
        <div className={"popup-contact__header"}>
          <h3>(: פניתך התקבלה בהצלחה</h3>
        </div>
        <div className={"popup-contact__text"}>
          <span>ניצור עמך קשר בהקדם האפשרי</span>
        </div>
      </div>
    </div>
  );
};

export default PopupContactForm;
