import React, { useEffect, useState } from "react";
import DayPicker from "react-day-picker";
import { useRouteMatch } from "react-router-dom";
import MomentLocaleUtils from "react-day-picker/moment";
import "react-day-picker/lib/style.css";
import "moment/locale/he";

function Navbar({
  nextMonth,
  previousMonth,
  onPreviousClick,
  onNextClick,
  className,
}) {
  const MONTHS = [
    "ינואר",
    "פברואר",
    "מרץ",
    "אפריל",
    "מאי",
    "יוני",
    "יולי",
    "אוגוסט",
    "ספטמבר",
    "אוקטובר",
    "נובמבר",
    "דצמבר",
  ];

  const prev = MONTHS[previousMonth.getMonth()];
  const next = MONTHS[nextMonth.getMonth()];

  return (
    <div className={className}>
      <span
        className={"calendar-prev-button"}
        onClick={(e) => {
          e.preventDefault();
          onPreviousClick();
        }}
      >
        <img src="/img/leftt-arrow.png" alt="prev month" className={"mr-1"} />{" "}
        {prev}
      </span>
      <span
        className={"calendar-next-button"}
        onClick={(e) => {
          e.preventDefault();
          onNextClick();
        }}
      >
        {next}{" "}
        <img src="/img/right-arrow.png" alt="next month" className={"ml-1"} />
      </span>
    </div>
  );
}

export function CalendarCom({ getDate, remembered, mistake }) {
  const [selectedDay, setSelectedDay] = useState(new Date());
  const WEEKDAYS_SHORT = ["א", "ב", "ג", "ד", "ה", "ו", "ש"];
  let { url } = useRouteMatch();
  const today = new Date();
  const ref = React.createRef();
  const isSendMoney = url === "/send-money";

  useEffect(() => {
    if (isSendMoney) setSelectedDay(undefined);
  }, [isSendMoney]);

  useEffect(() => {
    if (
      selectedDay &&
      selectedDay.getDate() < today.getDate() &&
      selectedDay.getMonth() <= today.getMonth() &&
      selectedDay.getYear() <= today.getYear()
    )
      setSelectedDay(today);
  }, [selectedDay, today]);

  useEffect(() => {
    if (remembered) setSelectedDay(remembered);
  }, [remembered]);

  useEffect(() => {
    if (mistake && !selectedDay)
      ref.current.scrollIntoView({ block: "center", behavior: "smooth" });
  }, [mistake, selectedDay, ref]);

  const handleDayClick = (day, { selected }) => {
    setSelectedDay(selected ? undefined : day);
    if (getDate) {
      getDate(selected ? undefined : day);
    }
  };
  return (
    <div className={"position-relative"}>
      <DayPicker
        dir="ltr"
        selectedDays={selectedDay}
        navbarElement={<Navbar />}
        weekdaysShort={WEEKDAYS_SHORT}
        disabledDays={{ before: today }}
        localeUtils={MomentLocaleUtils}
        locale={"he"}
        onDayClick={handleDayClick}
      />
      <span className={"calendar-val-error"} ref={ref}>
        {mistake}
      </span>
    </div>
  );
}

export default CalendarCom;
