import {
  LAUNCH_MODAL_SUCCESS,
  LAUNCH_MODAL_FAILURE,
  LAUNCH_AFFILIATE_LOGIN,
  CONTACT_US_CLICKED,
  USER_LOGIN_CLICK,
  CHANGE_PASSWORD_CLICK,
  CLOSE_LOGIN_FORM,
  FORGOT_PASSWORD_CLICK,
  CONTACT_FORM_MODAL,
  USER_LOGIN_FAILED_MODAL,
  USER_CREATED_MODAL,
  NO_SEARCH_RESULTS,
  USER_EXIST_MODAL,
} from "../constants";

const initialState = {
  modalSuccess: false,
  modalFailure: false,
  modalContact: false,
  affiliateLogin: false,
  contactFormCalled: false,
  userLoginFail: false,
  createUserModal: false,
  eventsNotFound: false,
  userExistModal: false,
  loginStage: 0,
};

export const modalReducer = (state = initialState, action) => {
  switch (action.type) {
    case NO_SEARCH_RESULTS:
      return {
        ...state,
        eventsNotFound: !state.eventsNotFound,
      };
    case LAUNCH_MODAL_SUCCESS:
      return {
        ...state,
        modalSuccess: !state.modalSuccess,
      };
    case LAUNCH_MODAL_FAILURE:
      return {
        ...state,
        modalFailure: !state.modalFailure,
      };
    case USER_EXIST_MODAL:
      return {
        ...state,
        userExistModal: !state.userExistModal,
      };
    case CONTACT_FORM_MODAL:
      return {
        ...state,
        modalContact: !state.modalContact,
      };
    case USER_CREATED_MODAL:
      return {
        ...state,
        createUserModal: !state.createUserModal,
      };
    case LAUNCH_AFFILIATE_LOGIN:
      return {
        ...state,
        affiliateLogin: !state.affiliateLogin,
      };
    case USER_LOGIN_FAILED_MODAL:
      return {
        ...state,
        userLoginFail: !state.userLoginFail,
      };
    case CONTACT_US_CLICKED:
      return {
        ...state,
        contactFormCalled: !state.contactFormCalled,
      };
    case CLOSE_LOGIN_FORM:
      return {
        ...state,
        loginStage: action.payload,
      };
    case USER_LOGIN_CLICK:
      return {
        ...state,
        loginStage: action.payload,
      };
    case CHANGE_PASSWORD_CLICK:
      return {
        ...state,
        loginStage: action.payload,
      };
    case FORGOT_PASSWORD_CLICK:
      return {
        ...state,
        loginStage: action.payload,
      };

    default:
      return state;
  }
};

export default modalReducer;
