import {
  LAUNCH_MODAL_SUCCESS,
  LAUNCH_MODAL_FAILURE,
  LAUNCH_AFFILIATE_LOGIN,
  CONTACT_US_CLICKED,
  CLOSE_LOGIN_FORM,
  USER_LOGIN_CLICK,
  CHANGE_PASSWORD_CLICK,
  FORGOT_PASSWORD_CLICK,
  CONTACT_FORM_MODAL,
  USER_LOGIN_FAILED_MODAL,
  USER_CREATED_MODAL,
  NO_SEARCH_RESULTS,
  USER_EXIST_MODAL,
} from "../constants";

export const eventsNotFound = () => {
  return {
    type: NO_SEARCH_RESULTS,
  };
};

export const toggleModalSuccess = () => {
  return {
    type: LAUNCH_MODAL_SUCCESS,
  };
};

export const toggleModalFailure = () => {
  return {
    type: LAUNCH_MODAL_FAILURE,
  };
};

export const toggleUserExistModal = () => {
  return {
    type: USER_EXIST_MODAL,
  };
};

export const toggleModalContact = () => {
  return {
    type: CONTACT_FORM_MODAL,
  };
};

export const toggleAffiliateLogin = () => {
  return {
    type: LAUNCH_AFFILIATE_LOGIN,
  };
};

export const toggleCreateUserModal = () => {
  return {
    type: USER_CREATED_MODAL,
  };
};

export const toggleFailedUserLogin = () => {
  return {
    type: USER_LOGIN_FAILED_MODAL,
  };
};

export const contactActivated = () => {
  return {
    type: CONTACT_US_CLICKED,
  };
};

export const resetLogin = () => {
  return {
    type: CLOSE_LOGIN_FORM,
    payload: 0,
  };
};

export const userLoginClicked = () => {
  return {
    type: USER_LOGIN_CLICK,
    payload: 1,
  };
};
export const changePasswordClick = () => {
  return {
    type: CHANGE_PASSWORD_CLICK,
    payload: 2,
  };
};
export const forgotPasswordClick = () => {
  return {
    type: FORGOT_PASSWORD_CLICK,
    payload: 4,
  };
};
