import React, { useState } from "react";

export const BankData = ({ bankData }) => {
  const [isBankDataShown, setBankDataShown] = useState(false);
  const { ac_no, bank_name, branch } = bankData;

  return (
    <div className={"col-md-12 bank-data-position bank-data"}>
      <div className={"bank-data__content"}>
        <div className={"bank-data__content-header"}>
          <button
            className={"bank-data__button mr-2"}
            onClick={() => setBankDataShown((prevState) => !prevState)}
          >
            הצג פרטים
          </button>
          <span className={"fn-b"}>פרטי חשבון לזיכוי</span>
        </div>
        <div className={"bank-data__content-body fn-b"}>
          <ul>
            <li>{isBankDataShown ? ac_no : "*****"} :מס' חשבון</li>
            <li>| {branch} :סניף | </li>
            <li>{isBankDataShown ? bank_name : "*****"}:בנק</li>
          </ul>
        </div>
      </div>

      <div className={"bank-data__image"}>
        <img src="/img/Even-page-user-view-mobile-01.png" alt="symbol" />
      </div>
    </div>
  );
};

export default BankData;
