import React, { useEffect, useState } from "react";
import { useRouteMatch } from "react-router-dom";
import { EventPageStepOne, EventPageStepTwo } from "./index";
import { endpoint } from "../../../../config";
import { api } from "../../../../core/api";
import { Footer } from "../../common";

export const EventPage = () => {
  const [steps, setStep] = useState(1);
  const [currentEvent, setEvent] = useState({});
  const [guestData, setGuestData] = useState({});
  let { url } = useRouteMatch();

  const handleGuestData = (payload) => {
    setGuestData({ ...guestData, ...payload });
  };

  useEffect(() => {
    const id = url.replace("/event-page/", "").trim();
    api
      .get(`${endpoint.getEvents}/${id}`)
      .then((res) => {
        setEvent(res);
      })
      .catch((e) => console.log(e));
  }, [url]);

  const handleStep = () => {
    if (steps === 1) setStep((prevState) => prevState + 1);
    if (steps === 2) setStep((prevState) => prevState - 1);
  };

  if (steps === 1)
    return (
      <>
        <EventPageStepOne
          goNext={handleStep}
          event={currentEvent}
          submitAf={handleGuestData}
        />
        <Footer />
      </>
    );
  if (steps === 2)
    return (
      <>
        <EventPageStepTwo goBack={handleStep} submitAf={handleGuestData} />
        <Footer />
      </>
    );
};

export default EventPage;
