import { AUTHENTICATION, LOGOUT_USER } from "../constants";

const initialState = {
  authorised: false
};

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case AUTHENTICATION:
      return {
        ...state,
        authorised: true
      };
    case LOGOUT_USER:
      return (state = {});

    default:
      return state;
  }
};

export default authReducer;
