import React, { useRef, useState } from "react";
import { Formik } from "formik";
import { useOnClickOutside, emailValidator } from "./constants";
import { useDispatch, useSelector } from "react-redux";
import { api } from "../../../../core/api";
import { endpoint } from "../../../../config";
import {
  getItemLs,
  getToken,
  setItemLs,
  removeItemLs,
} from "../../../../core/api/login";
import { registerUserPassword } from "../../../redux/actions";

export const ChangePasswordLogin = ({ hide }) => {
  const ref = useRef();
  const [isEmailValid, setEmailValid] = useState(null);
  const password = useSelector((state) => state.user.password);
  const dispatch = useDispatch();
  const securityCode = getItemLs("security_code");

  useOnClickOutside(ref, () => hide());

  const handleChangePassword = async (oldPassword, newPassword, token) => {
    const payload = {
      oldPassword,
      newPassword,
    };

    await api
      .put(endpoint.changePassword, payload, token)
      .then(() => {
        setEmailValid(true);
        dispatch(registerUserPassword(newPassword));
        setItemLs("password", newPassword);
      })
      .catch(() => setEmailValid(false));
  };

  const handleResetPassword = async (password, security_code) => {
    const payload = {
      password,
      security_code,
    };

    await api
      .post(endpoint.resetPassword, payload)
      .then(() => {
        setEmailValid(true);
        dispatch(registerUserPassword(password));
        setItemLs("password", password);
        removeItemLs("security_code");
      })
      .catch(() => {
        setEmailValid(false);
      });
  };

  return (
    <div className={"change-password-login login-popup-general"} ref={ref}>
      <div className={"login-form__close hide-xs"} onClick={hide}>
        X
      </div>
      <div className={"change-password-login__header"}>
        <span className={"fn-b"}>החלף סיסמא</span>
      </div>
      <Formik
        initialValues={{
          changePasswordEnter: "",
          changePasswordConfirm: "",
        }}
        validate={(values) => {
          const errors = {};
          switch (true) {
            case !values.changePasswordEnter:
              errors.changePasswordEnter = "שדה חובה*";
              return errors;
            case !values.changePasswordConfirm:
              errors.changePasswordConfirm = "שדה חובה*";
              return errors;
            case values.changePasswordEnter !== values.changePasswordConfirm ||
              !emailValidator(values.changePasswordEnter):
              errors.changePasswordConfirm = "אופס! אימות הסיסמא נכשל, נסה שוב";
              return errors;
            default:
              return errors;
          }
        }}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(false);
        }}
      >
        {({
          values,
          errors,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          resetForm,
        }) => (
          <form onSubmit={handleSubmit}>
            <div className={"change-password-login__inputs position-relative"}>
              <div className="form-group text-right position-relative">
                <span className={"change-password__rules"}>
                  הסיסמא חייבת להכיל 2 אותיות ו-4 מספרים
                </span>
                <input
                  type="password"
                  name="changePasswordEnter"
                  value={values.changePasswordEnter}
                  onChange={(e) => {
                    setEmailValid(null);
                    handleChange(e);
                  }}
                  onBlur={handleBlur}
                  placeholder={"סיסמא חדשה"}
                  disabled={isEmailValid}
                  autoComplete={"changePasswordEnter"}
                  className={"form-control fc"}
                />
              </div>
              <div className="form-group text-right">
                <input
                  type="password"
                  name="changePasswordConfirm"
                  value={values.changePasswordConfirm}
                  placeholder={"אימות סיסמא"}
                  onChange={(e) => {
                    setEmailValid(null);
                    handleChange(e);
                  }}
                  onBlur={handleBlur}
                  disabled={isEmailValid}
                  autoComplete={"changePasswordConfirm"}
                  className={"form-control fc"}
                />
              </div>
              {isEmailValid === null ? null : isEmailValid ? (
                <span className={"change-password__validation-result"}>
                  !יש הסיסמה הוחלפה בהצלחה
                  <i className="far fa-check-circle" />
                </span>
              ) : (
                <span className={"change-password__validation-result"}>
                  אופס! אימות הסיסמא נכשל, נסה שוב
                  <i className="far fa-times-circle" />
                </span>
              )}
            </div>

            <div className={"change-password-login__footer"}>
              <button
                className={"fn-b"}
                type="submit"
                disabled={isSubmitting}
                onClick={async () => {
                  if (!securityCode) {
                    if (
                      errors.changePasswordConfirm ||
                      !values.changePasswordEnter ||
                      !values.changePasswordConfirm ||
                      password === values.changePasswordEnter
                    ) {
                      setEmailValid(false);
                      resetForm();
                    } else {
                      await handleChangePassword(
                        password,
                        values.changePasswordEnter,
                        getToken()
                      );
                    }
                  } else {
                    if (
                      errors.changePasswordConfirm ||
                      !values.changePasswordEnter ||
                      !values.changePasswordConfirm
                    ) {
                      setEmailValid(false);
                      resetForm();
                    } else {
                      await handleResetPassword(
                        values.changePasswordEnter,
                        securityCode
                      );
                    }
                  }
                }}
              >
                אישור
              </button>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
};

export default ChangePasswordLogin;
