import { api } from "../../../../core/api";
import { endpoint } from "../../../../config";
import { dateFormatter } from "../../common";

export const searchUrlCreator = (eventData) => {
  const validData = {};
  let url = "";

  if (eventData.eventName) validData.event_title = eventData.eventName;
  if (eventData.eventType) validData.event_type = eventData.eventType;
  if (eventData.eventOwnerName)
    validData.author_name = eventData.eventOwnerName;
  if (eventData.eventDate)
    validData.event_date = dateFormatter(eventData.eventDate);

  for (let i in validData) {
    url += `&${i}=${validData[i]}`;
  }

  return url;
};

export const searchEvents = (pagination, params) => {
  const { pageLimit, pageNumber } = pagination;
  const searchUrl = `${endpoint.getEvents}?limit=${pageLimit}&page=${pageNumber}${params}`;

  return api.get(searchUrl);
};

export const sendMoneyInputValues = {
  uniqueCode: "",
  searchBar: "",
  eventName: "",
  eventType: "",
  eventOwnerName: "",
  eventDate: "",
};

export const clearAllOtherFields = (set) => {
  set("eventName", "");
  set("eventType", "");
  set("eventOwnerName", "");
  set("eventDate", undefined);
};

export const clearSearchBar = (set) => {
  set("searchBar", "");
};

export const elemsPerPage = 5;
