import React, { useState } from "react";
import { Footer, Hero } from "../../common";
import {
  InnerHero,
  QuestionCard,
  faqCardHeaderSettings,
  faqCardBodySettings,
  questions,
} from "./index";

export const Faq = () => {
  const [isOpen, setToggle] = useState([false, false, false, false]);

  const handleToggle = (param) => {
    setToggle(
      isOpen.map((el, index) => {
        if (index === param) return (el = !el);
        else return (el = false);
      })
    );
  };

  return (
    <>
      <Hero imgSrc="/img/Faq-bg.jpg">
        <InnerHero />
      </Hero>
      <section id="faq">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="text-center">
                <div className="heading-overline mt-md-5 mt-sm-4 mt-3 mb-3" />
                <div className="title-desc">
                  <h3>
                    <span className="fn-b">?</span>אז איך
                    <span className="fn-b"> זה עובד</span>
                  </h3>
                </div>
              </div>
            </div>
          </div>
          <div className={"row"}>
            <div className="faq-section col-md-12 col-sm-12 col-12 d-flex">
              <div className="col-md-1 col-sm-1 col-1" />
              <div
                className="accordion md-accordion col-md-10 col-sm-10 col-10"
                id="accordion"
                role="tablist"
                aria-multiselectable="true"
              >
                {questions.map((question, index) => {
                  return (
                    <QuestionCard
                      key={"question " + question.header}
                      header={question.header}
                      body={question.body}
                      headerSettings={faqCardHeaderSettings[index]}
                      bodySettings={faqCardBodySettings[index]}
                      isOpen={isOpen[index]}
                      index={index}
                      toggle={handleToggle}
                    />
                  );
                })}
              </div>
              <div className="col-md-1 col-sm-1 col-1" />
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Faq;
