import React from "react";
import AppRouter from "./routes";
import store from "../core/store";
import { Provider } from "react-redux";
import {
  registerUserName,
  setAuth,
  registerUserEmail,
  registerUserId,
  registerUserPassword,
} from "./redux/actions";
import "./styles/fontawosome/css/all.min.css";
import "./styles/bootstrap.min.css";
import "./styles/style.css";
import "./styles/responsive.css";
import "./styles/style.scss";

import "./containers";
import { getItemLs } from "../core/api/login";

const token = getItemLs("token");
const username = getItemLs("username");
const email = getItemLs("email");
const id = getItemLs("id");
const password = getItemLs("password");

token &&
  store.dispatch(setAuth()) &&
  store.dispatch(registerUserName(username)) &&
  store.dispatch(registerUserEmail(email)) &&
  store.dispatch(registerUserId(id)) &&
  store.dispatch(registerUserPassword(password));

const App = () => (
  <Provider store={store}>
    <AppRouter />
  </Provider>
);

export default App;
