import React from "react";
import { PaginationItem, EventPagination } from "../index";

export const SearchResult = ({
  events,
  pagination,
  setPage,
  totalEventsCount,
}) => {
  const { page, elemsPerPage } = pagination;

  return events.length > 0 ? (
    <section id="pagination">
      <div className="container">
        <div className="row">
          <div className="div-title text-center mb-5 hide-xs">
            <div className="heading-overline mb-3" />
            <div className="title-desc">
              <h3>.דברו איתנו, חופשי</h3>
            </div>
          </div>
          {events.map((event) => {
            return (
              <PaginationItem
                key={"event " + Math.random() + event.title}
                eventName={event.title}
                eventDetails={{
                  date: event.date,
                  venue: event.venue,
                  id: event._id,
                  image: event.thumbnail,
                }}
              />
            );
          })}
          <div className="col-md-12">
            <EventPagination
              page={page}
              setPage={setPage}
              perPage={elemsPerPage}
              total={totalEventsCount}
            />
          </div>
        </div>
      </div>
    </section>
  ) : null;
};

export default SearchResult;
