import React, { useCallback } from "react";
import { useDispatch } from "react-redux";
import { Formik } from "formik";
import classNames from "classnames";

import {
  loginUser,
  forgotPasswordClick,
  changePasswordClick,
} from "../../../redux/actions";
import { condition } from "../../views/CreateNewEvent";
import { useWindowSize } from "../../views/marketer/MarketerPage/Marketer_page";
import { ValidationMistakeText } from "../index";
import { getItemLs } from "../../../../core/api/login";

export function LoginForm({ toggle }) {
  const dispatch = useDispatch();
  const [width] = useWindowSize();
  const securityCode = getItemLs("security_code");

  const handleLogin = useCallback(
    async (email, password) => {
      try {
        await dispatch(loginUser(email, password));
        toggle();
      } catch (e) {
        toggle();
      }
    },
    [toggle, dispatch]
  );

  const handleForgotPassword = async () => {
    if (securityCode) {
      await dispatch(changePasswordClick());
    } else await dispatch(forgotPasswordClick());

    toggle();
  };

  return (
    <div className={"login-form-container login-popup-general"}>
      <div className={"login-form__close hide-xs"} onClick={() => toggle()}>
        X
      </div>
      <Formik
        initialValues={{
          loginEmail: "",
          password: "",
        }}
        validate={(values) => {
          const errors = {};
          condition(values, errors);
          return errors;
        }}
        onSubmit={(values, { setSubmitting }) => {
          handleLogin(values.loginEmail, values.password);
          setSubmitting(false);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
        }) => (
          <form onSubmit={handleSubmit}>
            <div className={"login-form__body position-relative"}>
              <div className="form-group text-right">
                <input
                  type="text"
                  name="loginEmail"
                  className={classNames(
                    "form-control fc",
                    errors.loginEmail &&
                      touched.loginEmail &&
                      errors.loginEmail &&
                      "fc-wrong"
                  )}
                  value={values.userName}
                  placeholder={"מייל"}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <ValidationMistakeText
                  absolute
                  mistake={
                    errors.loginEmail && touched.loginEmail && errors.loginEmail
                  }
                />
              </div>
              <span
                className={"forgot-password-link"}
                onClick={handleForgotPassword}
              >
                (שכחתי סיסמה)
              </span>
              <div className="form-group text-right login-email-mg">
                <input
                  type="password"
                  name="password"
                  value={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={"סיסמא"}
                  autoComplete={"password"}
                  className={classNames(
                    "form-control fc",
                    errors.password &&
                      touched.password &&
                      errors.password &&
                      "fc-wrong"
                  )}
                />
                <ValidationMistakeText
                  absolute
                  mistake={
                    errors.password && touched.password && errors.password
                  }
                />
              </div>
            </div>
            <div className={"login-form__footer"}>
              <div className={"login-form__footer__login-btn"}>
                <button type="submit" disabled={isSubmitting}>
                  {width > 575 ? "התחבר" : "בעל אירוע"}
                </button>
              </div>
              <div
                className={
                  "login-form__footer__hidden-btn hide-md hide-lg hide-xl hide-sm"
                }
              >
                <button type="submit" disabled={isSubmitting}>
                  משווק
                </button>
              </div>
              <div className={"login-form__footer__social-links hide-xs"}>
                <div>
                  <i className="fab fa-facebook-f" />
                </div>
                <div>
                  <i className="fab fa-google" />
                </div>
              </div>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
}

export default LoginForm;
