import React, { useState } from "react";
import moment from "moment";
import "moment/locale/he";
import { Formik } from "formik";
import classNames from "classnames";

import { InnerHero, eventPageStepOneValues } from "../index";
import { Hero, ValidationMistakeText } from "../../../common";
import { condition } from "../../CreateNewEvent";
import { useWindowSize } from "../../marketer/MarketerPage/Marketer_page";

function EventPageStepOne({ goNext, event, submitAf }) {
  const [width] = useWindowSize();
  const [videoFile, setVideoFile] = useState(null);
  const [audioFile, setAudioFile] = useState(null);
  const [photoFile, setPhotoFile] = useState(null);

  const formateDate = (date) => {
    const fullDate = moment(date).locale("he").format("l");

    return fullDate;
  };
  return (
    <>
      <Hero imgSrc="/img/Even-page-(user-view)-01.jpg">
        <InnerHero />
      </Hero>

      <section>
        <div className={"container flexible-container"}>
          <div className={"col-md-2"} />
          <div className={"col-md-4 text-center"}>
            <img src="/img/date.png" alt="date" className={"date"} />
            <span>{formateDate(event.date)}</span>
          </div>
          <div className={"col-md-4 text-center"}>
            <img
              src="/img/location.png"
              alt="location"
              className={"location"}
            />
            <span>{event.venue}</span>
          </div>
          <div className={"col-md-2"} />
        </div>
      </section>

      <Formik
        initialValues={eventPageStepOneValues}
        validate={(values) => {
          const errors = {};
          if (
            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(
              values.affiliateEmail
            )
          ) {
            errors.affiliateEmail = 'פורמט דוא"ל לא חוקי';
          }
          condition(values, errors);
          return errors;
        }}
        onSubmit={(values, { setSubmitting }) => {
          submitAf(values);
          setSubmitting(false);
          goNext();
          window.scrollTo(0, 0);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
        }) => (
          <form onSubmit={handleSubmit}>
            <section id="services">
              <div className="container">
                <div className="row affiliate-personal">
                  <div className="div-title text-center">
                    <div className="heading-overline mb-3" />
                    <div className="title-desc">
                      <h3>פרטים אישיים</h3>
                    </div>
                  </div>
                  <div className="col-md-4 col-sm-12">
                    <div className="from-wrapper">
                      <div className="form-group text-right d-inline-block mt-md-5 mt-sm-3 mt-4">
                        <label htmlFor="affiliateEmail">דוא“ל</label>
                        <input
                          type="text"
                          name="affiliateEmail"
                          className={classNames(
                            "form-control fc",
                            errors.affiliateEmail &&
                              touched.affiliateEmail &&
                              errors.affiliateEmail &&
                              "fc-wrong"
                          )}
                          value={values.affiliateEmail}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.affiliateEmail &&
                            touched.affiliateEmail &&
                            errors.affiliateEmail
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4 col-sm-6">
                    <div className="from-wrapper">
                      <div className="form-group text-right d-inline-block mt-md-5 mt-sm-5 mt-0">
                        <label htmlFor="affiliatePhone">נייד</label>
                        <input
                          type="text"
                          name="affiliatePhone"
                          className={classNames(
                            "form-control fc",
                            errors.affiliatePhone &&
                              touched.affiliatePhone &&
                              errors.affiliatePhone &&
                              "fc-wrong"
                          )}
                          value={values.affiliatePhone}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.affiliatePhone &&
                            touched.affiliatePhone &&
                            errors.affiliatePhone
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4 col-sm-6">
                    <div className="from-wrapper">
                      <div className="form-group text-right d-inline-block mt-md-5 mt-sm-5 mt-0">
                        <label htmlFor="affiliateName">שם נותן המתנה</label>
                        <input
                          type="text"
                          name="affiliateName"
                          className={classNames(
                            "form-control fc",
                            errors.affiliateName &&
                              touched.affiliateName &&
                              errors.affiliateName &&
                              "fc-wrong"
                          )}
                          value={values.affiliateName}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.affiliateName &&
                            touched.affiliateName &&
                            errors.affiliateName
                          }
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row m-service hide-xs">
                  <div className="col-md-12">
                    <div className="service-desc text-right">
                      <div className={"img-logo ml-3"} />
                      <h3>.פרטיך האישיים ישמשו אותנו לצורך שליחת קבלה בלבד</h3>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section>
              <div className={"container mt-5"}>
                <div className="form-group text-right event-page-form-button-center">
                  <label htmlFor="affiliateMessage">זה הזמן לשפוך את הלב</label>
                  <textarea
                    name={"affiliateMessage"}
                    cols="40"
                    rows="5"
                    className={classNames(
                      "form-control fc text-right textarea-xs mt-md-3 mt-sm-3",
                      errors.affiliateMessage &&
                        touched.affiliateMessage &&
                        errors.affiliateMessage &&
                        (width > 575 ? "fc-wrong" : "fc-wrong-textarea")
                    )}
                    value={values.affiliateMessage}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <ValidationMistakeText
                    mistake={
                      errors.affiliateMessage &&
                      touched.affiliateMessage &&
                      errors.affiliateMessage
                    }
                  />
                </div>
                <div className="row reversed-container no-mb btn-ev">
                  <div
                    className={
                      "col-md-4 col-sm-3 col-12 event-page-form-button-center"
                    }
                  >
                    <button className={"event-page-form-button"}>
                      בחר ברכה
                    </button>
                  </div>
                  <div className="col-md-8 col-sm-9 col-12 text-right">
                    <div className="service-desc event-page-form-button-center">
                      <div className={"img-logo ml-3 hide-xs"} />
                      <h3>!חסרי השראה? מזל שחשבנו על כמה ברכות לפני</h3>
                    </div>
                  </div>
                </div>

                <div className={"picture-md hide-xs"}>
                  <div className="add-file-image">
                    <label className="label">
                      <img src="/img/Event-Page-mobile-icons-01.png" alt={""} />
                      <input
                        type="file"
                        name="sendPhoto"
                        accept="image/*"
                        onChange={(e) =>
                          setPhotoFile({ file: e.target.files[0] })
                        }
                      />
                    </label>
                  </div>
                </div>

                <div
                  className={
                    "video-audio-congratulations hide-sm hide-md hide-lg hide-xl"
                  }
                >
                  <div className={"video"}>
                    <div className="add-file-image">
                      <label className="label">
                        <img
                          src="/img/Event-Page-mobile-icons-03.png"
                          alt={""}
                        />
                        <input
                          type="file"
                          name="sendVideo"
                          accept="video/mp4,video/x-m4v,video/*"
                          onChange={(e) =>
                            setVideoFile({ file: e.target.files[0] })
                          }
                        />
                      </label>
                    </div>
                  </div>
                  <div className={"audio"}>
                    <div className="add-file-image">
                      <label className="label">
                        <img
                          src="/img/Event-Page-mobile-icons-02.png"
                          alt={""}
                        />
                        <input
                          type="file"
                          name="sendAudio"
                          accept="audio/*"
                          onChange={(e) =>
                            setAudioFile({ file: e.target.files[0] })
                          }
                        />
                      </label>
                    </div>
                  </div>
                  <div className={"picture"}>
                    <div className="add-file-image">
                      <label className="label">
                        <img
                          src="/img/Event-Page-mobile-icons-01.png"
                          alt={""}
                        />
                        <input
                          type="file"
                          name="sendPhoto"
                          accept="image/*"
                          onChange={(e) =>
                            setPhotoFile({ file: e.target.files[0] })
                          }
                        />
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section id="link">
              <div className="container">
                <div className="row">
                  <div className="col-md-12">
                    <div className="title-outer">
                      <div className="title-inner-1" />
                      <div className="title-inner-2" />
                    </div>
                    <div className="link-btn text-center mt-5">
                      <button
                        type={"submit"}
                        className="event-form-button"
                        disabled={isSubmitting}
                      >
                        !סיימתי
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </form>
        )}
      </Formik>
    </>
  );
}

export default EventPageStepOne;
