import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Formik } from "formik";
import classNames from "classnames";

import {
  InnerHero,
  SearchResult,
  sendMoneyInputValues,
  elemsPerPage,
  searchUrlCreator,
  searchEvents,
  clearAllOtherFields,
  clearSearchBar,
  EventsNotFound,
} from "./index";
import { eventsNotFound } from "../../../redux/actions";
import { Hero, Calendar, Footer } from "../../common";
import { options } from "../CreateNewEvent";

export const SendMoney = () => {
  const [allEvents, setEvents] = useState([]);
  const [page, setPage] = useState(1);
  const [totalEventCount, setTotalEventCount] = useState(1);
  const [inputValues, setInputValues] = useState(null);
  const [isSelectActive, setSelectActive] = useState(false);
  const noEvents = useSelector((state) => state.modal.eventsNotFound);
  const dispatch = useDispatch();

  const handleSearch = useCallback(
    async (inputValues) => {
      try {
        await searchEvents(
          { pageLimit: elemsPerPage, pageNumber: page },
          searchUrlCreator(inputValues)
        ).then((res) => {
          setTotalEventCount(res.count);
          setEvents(res.results);
          if (!res.count) dispatch(eventsNotFound());
        });
      } catch (e) {
        console.log(e.message);
      }
    },
    [page, dispatch]
  );

  useEffect(() => {
    if (inputValues) handleSearch(inputValues);
  }, [handleSearch, inputValues]);

  return (
    <>
      <Hero imgSrc="/img/Giver-Send-money-page-01.jpg">
        <InnerHero />
      </Hero>
      <Formik
        initialValues={sendMoneyInputValues}
        onSubmit={async (values) => {
          if (noEvents) await dispatch(eventsNotFound());
          setInputValues(values);
        }}
      >
        {({
          values,
          handleChange,
          handleBlur,
          handleSubmit,
          setFieldValue,
        }) => (
          <form onSubmit={handleSubmit}>
            <section id="big-heading">
              <div className="container">
                <div className="row send-money__input_text hide-xl hide-lg hide-md hide-sm">
                  <div className="col-md-12 text-center">
                    <input
                      type={"search"}
                      name={"searchBar"}
                      className="big-heading search-small"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.searchBar}
                      placeholder={
                        "(..חיפוש חופשי (שם בעל האירוע, מיקום, תאריך"
                      }
                    />
                  </div>
                </div>
                <div className="row send-money__input_text hide-xs">
                  <div className="col-md-10">
                    <input type={"text"} className="big-heading" />
                  </div>
                  <h3>קוד אירוע</h3>
                </div>
                <div className="row send-money__input_text hide-xs">
                  <div className="col-md-12">
                    <input
                      type={"search"}
                      name={"searchBar"}
                      placeholder={
                        "(..חיפוש חופשי (שם בעל האירוע, מיקום, תאריך"
                      }
                      className="big-heading placeholder-size"
                      onChange={(e) => {
                        handleChange(e);
                        clearAllOtherFields(setFieldValue);
                      }}
                      onBlur={handleBlur}
                      value={values.searchBar}
                    />
                  </div>
                </div>
              </div>
            </section>

            <section id="services">
              <div className="container">
                <div className="row">
                  <div className="div-title text-center">
                    <div className="heading-overline mt-md-5 mt-sm-5 mt-3 mb-3" />
                    <div className="title-desc">
                      <h3>חיפוש אירוע</h3>
                    </div>
                  </div>
                  <div className="col-md-12 col-6">
                    <div className="from-wrapper form-send-money-pd">
                      <div className="form-group text-right d-inline-block mt-5">
                        <label htmlFor="eventName">שם האירוע</label>
                        <input
                          type="text"
                          name="eventName"
                          className="form-control fc"
                          onChange={(e) => {
                            handleChange(e);
                            clearSearchBar(setFieldValue);
                          }}
                          onBlur={handleBlur}
                          value={values.eventName}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6 col-6">
                    <div className="from-wrapper form-send-money-pd">
                      <div className="form-group text-right d-inline-block mt-5 position-relative">
                        <div
                          className={classNames(
                            "img-arrow-marketer-search",
                            isSelectActive && "rotated"
                          )}
                        />
                        <label htmlFor="eventType">סוג האירוע</label>
                        <select
                          className="form-control fc type-select"
                          name="eventType"
                          value={values.eventType}
                          onChange={(e) => {
                            handleChange(e);
                            clearSearchBar(setFieldValue);
                          }}
                          onClick={() =>
                            setSelectActive((prevState) => !prevState)
                          }
                          onBlur={() => {
                            return isSelectActive
                              ? setSelectActive((prevState) => !prevState)
                              : null;
                          }}
                        >
                          <option label={" "} />
                          {options.map((option) => (
                            <option key={option}>{option}</option>
                          ))}
                        </select>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6 col-12">
                    <div className="from-wrapper form-send-money-pd">
                      <div className="form-group text-right d-inline-block mt-5">
                        <label htmlFor="eventOwnerName">שם בעל האירוע</label>
                        <input
                          type="text"
                          name="eventOwnerName"
                          className="form-control fc"
                          onChange={(e) => {
                            handleChange(e);
                            clearSearchBar(setFieldValue);
                          }}
                          onBlur={handleBlur}
                          value={values.eventOwnerName}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section id={"calendar"}>
              <div className={"hide-xs"}>
                <Calendar
                  getDate={(value) => {
                    setFieldValue("eventDate", value);
                    setFieldValue("searchBar", "");
                  }}
                />
              </div>
              <div className="col-md-12 text-center">
                <button
                  type="submit"
                  className="search-btn btn event-2 mt-3 mt smoney-button"
                >
                  חפש
                </button>
              </div>
            </section>
            {noEvents ? <EventsNotFound /> : null}
            <SearchResult
              events={allEvents}
              pagination={{ page, elemsPerPage }}
              setPage={setPage}
              totalEventsCount={totalEventCount}
            />
          </form>
        )}
      </Formik>
      <Footer />
    </>
  );
};

export default SendMoney;
