import Pagination from "react-js-pagination";
import React from "react";

export const EventPagination = ({ page, setPage, perPage, total }) => {
  return (
    <Pagination
      activePage={page}
      itemsCountPerPage={perPage}
      totalItemsCount={total}
      hideFirstLastPages
      activeClass={"new-pagination-active"}
      innerClass={"new-pagination"}
      nextPageText={
        <span aria-hidden="true">
          <img src="img/right-arrow.png" alt="" />
        </span>
      }
      prevPageText={
        <span aria-hidden="true">
          <img src="img/leftt-arrow.png" alt="" />
        </span>
      }
      onChange={(pageNumber) => {
        setPage(pageNumber);
      }}
    />
  );
};

export default EventPagination;
