import { AUTHENTICATION, LOGOUT_USER } from "../constants";
import { api } from "../../../core/api";
import { endpoint } from "../../../config";
import { getToken, setItemLs, setToken } from "../../../core/api/login";
import {
  registerUserEmail,
  registerUserId,
  registerUserName,
  registerUserPassword,
  registerUserToken,
} from "./user";
import { toggleFailedUserLogin } from "./modal";
import { getEvents } from "./create_event";

export const setAuth = () => {
  return {
    type: AUTHENTICATION,
  };
};

export const logOut = () => {
  return {
    type: LOGOUT_USER,
  };
};

export const loginUser = (email, password) => (dispatch) => {
  api
    .post(
      endpoint.loginUser,
      {
        email,
        password,
      },
      getToken()
    )
    .then((response) => {
      dispatch(registerUserName(response.user.fullname));
      dispatch(registerUserToken(response.token.split(" ")[1]));
      dispatch(registerUserId(response.user._id));
      dispatch(registerUserEmail(response.user.email));
      dispatch(registerUserPassword(password));
      dispatch(setAuth());
      setToken(response.token);
      setItemLs("username", response.user.fullname);
      setItemLs("password", password);
      setItemLs("id", response.user._id);
      setItemLs("email", response.user.email);
    })
    .catch(() => {
      dispatch(toggleFailedUserLogin());
    });
};

export const logoutUser = () => (dispatch) => {
  dispatch(logOut());
  dispatch(registerUserName(""));
  dispatch(registerUserEmail(""));
  dispatch(registerUserToken(""));
  dispatch(registerUserId(""));
  dispatch(getEvents([]));
};
