import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import classNames from "classnames";
import { Formik } from "formik";
import {
  Hero,
  Calendar,
  ValidationMistakeText,
  Footer,
  nameFormatter,
  phoneNumberFormatter,
  phoneMinusParser,
  strCutter,
  PopupUserExist,
  InlineCalendar,
} from "../../../common";
import { stepOneValues, InnerHero, uploadPhoto } from "../index";
import {
  fillCurrentEventData,
  setupEventOnServer,
  ifUserExist,
  toggleUserExistModal,
  clearCurrentEvent,
} from "../../../../redux/actions";
import { defaultImage } from "../../../../../config";
import { history } from "../../../../routes/AppRouter";
import { getToken } from "../../../../../core/api/login";
import { Select } from "./Select";

export const CreateNewEventStepOne = () => {
  const [eventImage, setEventImage] = useState(null);
  const [serverImage, setServerImage] = useState(null);
  const [rememberedDay, setRememberedDay] = useState(null);
  const auth = useSelector((state) => state.auth.authorised);
  const event = useSelector((state) => state.crEvent.currentEvent);
  const userExistModal = useSelector((state) => state.modal.userExistModal);
  const [currentPhoneValue, setCurrentPhoneValue] = useState("");
  const prevPhoneValue = useRef("0");
  const dispatch = useDispatch();

  useEffect(() => {
    if (event) setRememberedDay(new Date(event.eventDate));
  }, [event]);

  useEffect(() => {
    if (
      auth &&
      event &&
      event.eventDate &&
      event.eventType &&
      event.eventName
    ) {
      dispatch(setupEventOnServer(event, getToken()));
    }
  }, [auth, event, dispatch]);

  useEffect(() => {
    if (eventImage) uploadPhoto(eventImage, setServerImage);
  }, [eventImage]);

  useEffect(() => {
    prevPhoneValue.current = currentPhoneValue;
  }, [currentPhoneValue]);

  const handleEnterNumber = (value, set, cutter) => {
    if (value.length !== 3) return value;
    if (phoneMinusParser(value)) set("phoneNumber", value + "-");
    if (prevPhoneValue.current === value) set("phoneNumber", cutter(value));
  };

  const handleStepOne = (values, disable) => {
    dispatch(
      fillCurrentEventData({
        ...values,
        serverImage: serverImage || defaultImage,
      })
    );
    disable(false);

    if (!auth) {
      history.push("/create-new-event/step-2");
      window.scrollTo(0, 0);
    } else {
      history.push("/event-hall");
    }
  };

  return (
    <>
      <Hero imgSrc="/img/bg-Create-new-Event-01.jpg">
        <InnerHero />
      </Hero>
      <Formik
        initialValues={
          userExistModal ? stepOneValues : event ? event : stepOneValues
        }
        validate={(values) => {
          const errors = {};

          switch (true) {
            case !auth && !values.username:
              errors.username = "שדה חובה*";
              return errors;
            case !auth && !nameFormatter(values.username):
              errors.username = "שם אינו תקין*";
              return errors;
            case !auth && !values.phoneNumber:
              errors.phoneNumber = "שדה חובה*";
              return errors;
            case !auth && !phoneNumberFormatter(values.phoneNumber):
              errors.phoneNumber = "מספר נייד שגוי*";
              return errors;
            case !auth && !values.email:
              errors.email = "שדה חובה*";
              return errors;
            case !auth &&
              !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email):
              errors.email = 'כתובת דוא"ל אינה תקינה*';
              return errors;
            case !values.eventName:
              errors.eventName = "שדה חובה*";
              return errors;
            case !values.eventPlace:
              errors.eventPlace = "שדה חובה*";
              return errors;
            case !values.eventType:
              errors.eventType = "שדה חובה*";
              return errors;
            case !values.eventDate:
              errors.eventDate = "יש לבחור את תאריך האירו*";
              return errors;
            default:
              return errors;
          }
        }}
        onSubmit={async (
          values,
          { setSubmitting, resetForm, setFieldValue }
        ) => {
          try {
            if (!auth) {
              const { succss } = await ifUserExist(values.email);
              if (succss) {
                throw Error;
              } else {
                handleStepOne(values, setSubmitting);
              }
            } else {
              handleStepOne(values, setSubmitting);
            }
          } catch (e) {
            await dispatch(toggleUserExistModal());
            await dispatch(clearCurrentEvent());
            await resetForm();
            await setFieldValue("eventDate", "");
          }
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue,
        }) => (
          <form onSubmit={handleSubmit}>
            {!auth ? (
              <section id="services">
                <div className="container">
                  <div className="row pr-4 pl-4 pr-md-0 pr-sm-0 pl-md-0 pl-sm-0">
                    <div className="div-title text-center">
                      <div className="heading-overline mt-md-5 mt-sm-4 mt-3 mb-3" />
                      <div className="title-desc">
                        <h3>שמחים להכיר</h3>
                      </div>
                    </div>
                    <div className="col-md-4 hide-lg hide-md hide-xl text-center mt-4 mb-3">
                      <a href="#/" className="btn btn-lg btn-facebook">
                        <i className="fab fa-facebook-f" />
                        <span className="btn-fb">הרשם דרך פייסבוק</span>
                      </a>
                    </div>

                    <div className="col-md-4 col-sm-12 col-12 hide-sm hide-xs">
                      <div className="from-wrapper">
                        <div className="form-group text-right d-inline-block mt-5">
                          <label htmlFor="email">דוא"ל</label>
                          <input
                            type="email"
                            name="email"
                            className={classNames(
                              "form-control fc",
                              errors.email &&
                                touched.email &&
                                errors.email &&
                                "fc-wrong"
                            )}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.email}
                          />
                          <ValidationMistakeText
                            mistake={
                              errors.email && touched.email && errors.email
                            }
                          />
                        </div>
                      </div>
                    </div>

                    <div className="col-md-4 col-sm-6 col-6">
                      <div className="from-wrapper">
                        <div className="form-group text-right d-inline-block mt-5">
                          <label htmlFor="phoneNumber">נייד</label>
                          <input
                            type="text"
                            name="phoneNumber"
                            className={classNames(
                              "form-control fc",
                              errors.phoneNumber &&
                                touched.phoneNumber &&
                                errors.phoneNumber &&
                                "fc-wrong"
                            )}
                            onChange={(e) => {
                              if (e.target.value.length < 12) {
                                setCurrentPhoneValue(e.target.value);
                                handleChange(e);
                              }
                            }}
                            onBlur={handleBlur}
                            value={handleEnterNumber(
                              values.phoneNumber,
                              setFieldValue,
                              strCutter
                            )}
                          />
                          <ValidationMistakeText
                            mistake={
                              errors.phoneNumber &&
                              touched.phoneNumber &&
                              errors.phoneNumber
                            }
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4 col-sm-6 col-6">
                      <div className="from-wrapper">
                        <div className="form-group text-right d-inline-block mt-5">
                          <label htmlFor="username">שם מלא</label>
                          <input
                            type="text"
                            name="username"
                            className={classNames(
                              "form-control fc",
                              errors.username &&
                                touched.username &&
                                errors.username &&
                                "fc-wrong"
                            )}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.username}
                          />
                          <ValidationMistakeText
                            mistake={
                              errors.username &&
                              touched.username &&
                              errors.username
                            }
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-12 hide-lg hide-md hide-xl">
                      <div className="from-wrapper">
                        <div className="form-group text-right d-inline-block mt-5">
                          <label htmlFor="email">דוא"ל</label>
                          <input
                            type="email"
                            name="email"
                            className={classNames(
                              "form-control fc",
                              errors.email &&
                                touched.email &&
                                errors.email &&
                                "fc-wrong"
                            )}
                            onChange={handleChange}
                            value={values.email}
                            onBlur={handleBlur}
                          />
                          <ValidationMistakeText
                            mistake={
                              errors.email && touched.email && errors.email
                            }
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row mt-5 mb-5 hide-sm hide-xs">
                    <div className="col-md-8">
                      <div className="service-desc centrify ">
                        <img
                          src="/img/logo.png"
                          alt="logo"
                          className={"ml-1 ml-md-3 mr-0"}
                        />
                        <h3>גיבר לא תפרסם שום מידע בשמך ברשתות החברתיות</h3>
                      </div>
                    </div>
                    <div className="col-md-4 hide-xs hide-sm">
                      <a href="#/" className="btn btn-lg btn-facebook">
                        <i className="fab fa-facebook-f" />
                        <span className="btn-fb">הרשם דרך פייסבוק</span>
                      </a>
                    </div>
                  </div>
                </div>
              </section>
            ) : null}

            <section id="services">
              <div className="container">
                <div className="row">
                  <div className="div-title text-center">
                    <div className="heading-overline mt-5 mt-sm-5 mb-3" />
                    <div className="title-desc">
                      <h3>פרטי האירוע</h3>
                    </div>
                  </div>
                </div>
                <div
                  className={"row pr-4 pl-4 pr-md-0 pr-sm-0 pl-md-0 pl-sm-0"}
                >
                  <div className="from-wrapper content-wrapper col-md-12">
                    <div className="form-group text-right d-inline-block mt-5">
                      <label htmlFor="eventName">כותרת האירוע</label>
                      <input
                        type="text"
                        name="eventName"
                        className={classNames(
                          "form-control fc",
                          errors.eventName &&
                            touched.eventName &&
                            errors.eventName &&
                            "fc-wrong"
                        )}
                        value={values.eventName}
                        onChange={handleChange}
                        onBlur={handleBlur}
                      />
                      <ValidationMistakeText
                        mistake={
                          errors.eventName &&
                          touched.eventName &&
                          errors.eventName
                        }
                      />
                    </div>

                    <div className="add-file col-1">
                      <div
                        className={classNames(
                          "form-group",
                          errors.eventName &&
                            touched.eventName &&
                            errors.eventName &&
                            "validation-on"
                        )}
                      >
                        <label className="label">
                          {serverImage ? (
                            <i className="fas fa-check-square" />
                          ) : (
                            <i className="fas fa-camera fa-3x" />
                          )}
                          <input
                            type="file"
                            name="eventImage"
                            onChange={(e) =>
                              setEventImage({ file: e.target.files[0] })
                            }
                          />
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row m-service-m hide-sm hide-xs">
                  <div className="col-md-12">
                    <div className="service-desc service-desc-m centrify">
                      <img src="/img/logo.png" alt="logo" />
                      <h3>
                        התמונה והכותרת יופיעו בראשית דף האירוע
                        <span className="fn-b"> ויקבלו את פני האורחים שלך</span>
                      </h3>
                    </div>
                  </div>
                </div>
                <div className="row pr-4 pl-4 pr-md-0 pr-sm-0 pl-md-0 pl-sm-0">
                  <div className="col-md-6">
                    <div className="from-wrapper">
                      <div className="form-group text-right d-inline-block">
                        <label htmlFor="eventPlace">מיקום האירוע </label>
                        <input
                          type="text"
                          name="eventPlace"
                          className={classNames(
                            "form-control fc",
                            errors.eventPlace &&
                              touched.eventPlace &&
                              errors.eventPlace &&
                              "fc-wrong"
                          )}
                          value={values.eventPlace}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.eventPlace &&
                            touched.eventPlace &&
                            errors.eventPlace
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="from-wrapper">
                      <div className="form-group text-right d-inline-block position-relative">
                        <Select
                          error={{
                            errors: errors.eventType,
                            touched: touched.eventType,
                          }}
                          value={values.eventType}
                          onChange={handleChange}
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.eventType &&
                            touched.eventType &&
                            errors.eventType
                          }
                        />
                      </div>
                    </div>
                  </div>

                  <div className="col-sm-12 col-12 hide-lg hide-xl hide-md mb-sm-5">
                    <div className="from-wrapper">
                      <div className="form-group text-right d-inline-block">
                        <label htmlFor="eventDate">תאריך האירוע</label>
                        <InlineCalendar
                          remembered={rememberedDay ? rememberedDay : null}
                          data={values.eventDate}
                          onChange={(value) =>
                            setFieldValue("eventDate", value)
                          }
                          customInput={
                            <input
                              className={classNames(
                                "form-control fc",
                                errors.eventDate &&
                                  touched.eventDate &&
                                  errors.eventDate &&
                                  "fc-wrong"
                              )}
                            />
                          }
                        />
                        <ValidationMistakeText
                          mistake={
                            errors.eventDate &&
                            touched.eventDate &&
                            errors.eventDate
                          }
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section id="event-calendar">
              <div className={"hide-xs hide-sm"}>
                <Calendar
                  getDate={(value) => setFieldValue("eventDate", value)}
                  remembered={rememberedDay ? rememberedDay : null}
                  mistake={errors.eventDate}
                />
              </div>
            </section>

            <section id="link">
              <div className="container cr-event-btn">
                <div className="row">
                  <div className="col-md-12">
                    <div className="title-outer">
                      <div className="title-inner-1" />
                      {!auth ? (
                        <div className="title-inner-2" />
                      ) : (
                        <div className="title-inner-1" />
                      )}
                    </div>
                    <div className="link-btn text-center">
                      <button
                        type="submit"
                        className="event-form-button"
                        disabled={isSubmitting}
                      >
                        המשך
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </form>
        )}
      </Formik>
      <Footer />
      {userExistModal ? <PopupUserExist /> : null}
    </>
  );
};

export default CreateNewEventStepOne;
