import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import classNames from "classnames";
import {
  LoginForm,
  UnloggedUser,
  LoggedUser,
  HeaderLink,
  UserClickLogin,
  PopupLoginFailed,
  ChangePasswordLogin,
  ForgotPasswordLogin,
  headerLinkSettings,
  useOnClickOutside,
} from "../index";

import {
  logoutUser,
  resetLogin,
  userLoginClicked,
  changePasswordClick,
} from "../../../redux/actions";
import { history } from "../../../routes/AppRouter";

const Header = () => {
  const [isBurgerOpen, setBurgerOpen] = useState(false);
  const [isScrolling, setScroll] = useState(false);
  const [isLoginShow, setLoginShow] = useState(false);
  const username = useSelector((state) => state.user.username);
  const loginStage = useSelector((state) => state.modal.loginStage);
  const userLoginFail = useSelector((state) => state.modal.userLoginFail);
  const dispatch = useDispatch();
  const ref = useRef();
  useOnClickOutside(ref, () => setLoginShow(false));

  const handleScroll = () => {
    const scroll = window.scrollY;
    setScroll(scroll > 1);
  };

  const handleToggleBurger = () => {
    if (isBurgerOpen) setBurgerOpen(!isBurgerOpen);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  });

  useEffect(() => {
    window.addEventListener("click", handleToggleBurger);
    return () => window.removeEventListener("click", handleToggleBurger);
  });

  const handleLogOut = async () => {
    try {
      await dispatch(logoutUser());
      await dispatch(resetLogin());
      localStorage.clear();
      history.push("/");
    } catch (e) {
      console.log(e.message);
    }
  };

  return (
    <header>
      {userLoginFail ? (
        <PopupLoginFailed text={"חשבון לא קיים"} nextTry={setLoginShow} />
      ) : null}
      <nav
        className={classNames(
          "navbar navbar-expand-xl fixed-top navbar-color navbar-light",
          (isBurgerOpen || isScrolling) && "giver-top-nav"
        )}
        id="mainNav"
      >
        <div className="container">
          <div className={"navbar-brand"}>
            <Link to="/">
              <img src="/img/logo.png" alt="" className="img-fluid" />
            </Link>
            <span className="di-none fn-b position-relative" ref={ref}>
              {username ? (
                <LoggedUser
                  username={username}
                  clickOnName={() => dispatch(userLoginClicked())}
                />
              ) : (
                <UnloggedUser click={setLoginShow} />
              )}
              {isLoginShow ? <LoginForm toggle={setLoginShow} /> : null}
              {loginStage === 1 ? (
                <UserClickLogin
                  username={username}
                  logout={handleLogOut}
                  hide={() => dispatch(resetLogin())}
                  changePassword={() => dispatch(changePasswordClick())}
                />
              ) : null}
              {loginStage === 2 ? (
                <ChangePasswordLogin hide={() => dispatch(resetLogin())} />
              ) : null}
              {loginStage === 4 ? (
                <ForgotPasswordLogin hide={() => dispatch(resetLogin())} />
              ) : null}
            </span>

            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarNavDropdown"
              aria-controls="navbarNavDropdown"
              aria-expanded="false"
              aria-label="Toggle navigation"
              onClick={() => setBurgerOpen((prev) => !prev)}
            >
              <span className="navbar-toggler-icon" />
            </button>
          </div>
          <div
            className={classNames(
              "collapse navbar-collapse",
              isBurgerOpen && "show"
            )}
            id="navbarNavDropdown"
          >
            <ul className="navbar-nav ml-auto">
              {headerLinkSettings.map((link) => {
                return (
                  <HeaderLink
                    key={"link " + link.text + Math.random()}
                    linkSettings={link}
                  />
                );
              })}
            </ul>
          </div>
        </div>
      </nav>
    </header>
  );
};

export default Header;
