import React from "react";
import classNames from "classnames";

export const QuestionCard = ({
  header,
  body,
  headerSettings,
  bodySettings,
  isOpen,
  toggle,
  index
}) => {
  const {
    headerId,
    headerClassName,
    dataToggle,
    headerDataParent,
    href,
    ariaExpanded,
    ariaControls
  } = headerSettings;
  const {
    bodyId,
    bodyClassName,
    role,
    ariaLabelledby,
    bodyDataParent
  } = bodySettings;

  return (
    <div className="card">
      <div className="card-header" role="tab" id={headerId}>
        <a
          className={headerClassName}
          data-toggle={dataToggle}
          data-parent={headerDataParent}
          href={href}
          aria-expanded={ariaExpanded}
          aria-controls={ariaControls}
          onClick={() => toggle(index)}
        >
          <img
            src={"/img/arrow.png"}
            alt={"open"}
            className={classNames("", isOpen && "rotated")}
          />
          <h3 className="mb-0">{header}</h3>
        </a>
      </div>

      <div
        id={bodyId}
        className={bodyClassName}
        role={role}
        aria-labelledby={ariaLabelledby}
        data-parent={bodyDataParent}
      >
        <div className="card-body">{body}</div>
      </div>
    </div>
  );
};

export default QuestionCard;
