import React, { useRef } from "react";
import { history } from "../../../routes/AppRouter";
import { useOnClickOutside } from "../index";

export const UserClickLogin = ({ username, logout, changePassword, hide }) => {
  const ref = useRef();
  useOnClickOutside(ref, () => hide());

  const handleGoToEventHall = async () => {
    await history.push("/event-hall");
    await hide();
  };

  return (
    <div className={"user-click-login-form login-popup-general"} ref={ref}>
      <div className={"login-form__close"} onClick={hide}>
        X
      </div>
      <span className={"user-click__username"}>
        <span
          className={"user-click__username-link"}
          onClick={handleGoToEventHall}
        >
          {username + " "}
        </span>
        היי{" "}
      </span>
      <button
        className={"user-click__change-password"}
        onClick={changePassword}
      >
        החלף סיסמא
      </button>
      <button className={"user-click__logout"} onClick={() => logout()}>
        התנתק
      </button>
    </div>
  );
};

export default UserClickLogin;
