import React from "react";
import {useWindowSize} from '../../views/marketer/MarketerPage/Marketer_page';

export function UnloggedUser({ click }) {
  const [width] = useWindowSize()
  return <span onClick={() => click(prev => !prev)}>{width > 575 ? "כניסת בעל אירוע" : "כניסת בעל אירוע / משווק"}</span>;
}

export default UnloggedUser;
